/**
 * Displays the Industry Code filter
 */
import React, { useContext } from "react";
import PropTypes from "prop-types";

import { FilterContext } from "../Search";

import { makeStyles } from "@material-ui/core/styles";
import Input from "@material-ui/core/Input";
import InputLabel from "@material-ui/core/InputLabel";
import MenuItem from "@material-ui/core/MenuItem";
import FormControl from "@material-ui/core/FormControl";
import Select from "@material-ui/core/Select";
import Chip from "@material-ui/core/Chip";
import Grid from "@material-ui/core/Grid";

import i18n from "../../../i18n";
import { useTranslation } from "react-i18next";
import { en_en } from "./FilterIndustryCode.lang.en-en";
import { nl_nl } from "./FilterIndustryCode.lang.nl-nl";
import { nl_be } from "./FilterIndustryCode.lang.nl-be";

i18n.addResourceBundle("en-EN", "FilterIndustryCode", en_en);
i18n.addResourceBundle("nl-NL", "FilterIndustryCode", nl_nl);
i18n.addResourceBundle("nl-BE", "FilterIndustryCode", nl_be);

/**
 * Defines the prop types
 */
const propTypes = {
  codes: PropTypes.arrayOf(PropTypes.string),
  selected: PropTypes.arrayOf(PropTypes.string)
};

/**
 * Defines the default props
 */
const defaultProps = {
  codes: [
    "sbi: 4323",
    "nace: 3456",
    "naics: 1111",
    "sic: 9999",
    "nace: 6666",
    "sbi: 4444",
    "sic: 3333"
  ],
  selected: []
};

/**
 * Styles the component
 */
const useStyles = makeStyles(theme => ({
  formControl: {
    margin: theme.spacing(1),
    minWidth: 120,
    maxWidth: 300,
    width: "100%"
  },
  chips: {
    display: "flex",
    flexWrap: "wrap"
  },
  chip: {
    margin: 2,
    // Wrap long text
    height: "auto",
    padding: 4,
    "& .MuiChip-label": {
      overflowWrap: "break-word",
      whiteSpace: "normal",
      lineHeight: "1"
    }
  },
  noLabel: {
    marginTop: theme.spacing(3)
  }
}));

/**
 * Displays the component
 */
const FilterIndustryCode = props => {
  const { codes, selected: defaultSelected } = props;
  const { filters, setFilters } = useContext(FilterContext);
  const selected = filters?.industryCode || defaultSelected;

  const { t } = useTranslation("FilterIndustryCode");
  const classes = useStyles();

  /**
   * Handles the select change
   */
  const selectHandler = data => {
    setFilters(prevState => {
      return { ...prevState, industryCode: data.target.value };
    });
  };

  const items = codes.map(code => (
    <MenuItem key={code} value={code}>
      {code}
    </MenuItem>
  ));

  return (
    <Grid item xs={12} className="FilterIndustryCode">
      <FormControl className={classes.formControl}>
        <InputLabel>{t("Industry code")}</InputLabel>
        <Select
          multiple
          name="industryCode"
          value={selected}
          onChange={selectHandler}
          input={<Input id="select-multiple-chip" />}
          renderValue={selected => (
            <div className={classes.chips}>
              {selected.map(value => (
                <Chip
                  size="small"
                  key={value}
                  label={value}
                  className={classes.chip}
                />
              ))}
            </div>
          )}
        >
          {items}
        </Select>
      </FormControl>
    </Grid>
  );
};

FilterIndustryCode.propTypes = propTypes;
FilterIndustryCode.defaultProps = defaultProps;

export default FilterIndustryCode;
export {
  propTypes as FilterIndustryCodePropTypes,
  defaultProps as FilterIndustryCodeDefaultProps
};
