/**
 * Displays the current active filters
 */
import React, { useContext } from "react";
import PropTypes from "prop-types";
import { isEmpty, capitalize } from "lodash";
import moment from "moment";

import { FilterContext } from "../Search";
import { FilterAgeDefaultProps } from "../FilterAge";

import { makeStyles } from "@material-ui/core/styles";
import Grid from "@material-ui/core/Grid";
import FormControl from "@material-ui/core/FormControl";
import Chip from "@material-ui/core/Chip";

import i18n from "../../../i18n";
import { useTranslation } from "react-i18next";
import { en_en } from "./FilteredBy.lang.en-en";
import { nl_nl } from "./FilteredBy.lang.nl-nl";
import { nl_be } from "./FilteredBy.lang.nl-be";

i18n.addResourceBundle("en-EN", "FilteredBy", en_en);
i18n.addResourceBundle("nl-NL", "FilteredBy", nl_nl);
i18n.addResourceBundle("nl-BE", "FilteredBy", nl_be);

/**
 * Defines the prop types
 */
const propTypes = {
  filters: PropTypes.arrayOf(PropTypes.string)
};

/**
 * Defines the default props
 */
const defaultProps = {
  filters: ["Filter 1", "Filter 2", "Filter 3", "Filter 4"]
};

/**
 * Styles the component
 */
const useStyles = makeStyles(theme => ({
  formControl: {
    margin: theme.spacing(1),
    minWidth: 120,
    maxWidth: 300,
    width: "100%"
  },
  chips: {
    display: "flex",
    flexWrap: "wrap"
  },
  chip: {
    margin: 2,
    // Wrap long text
    height: "auto",
    padding: 4,
    "& .MuiChip-label": {
      overflowWrap: "break-word",
      whiteSpace: "normal",
      lineHeight: "1"
    }
  },
  noLabel: {
    marginTop: theme.spacing(3)
  }
}));

/**
 * Checks if a filter is set
 */
const isFilterSet = filter => {
  const value = filter[1];

  switch (typeof value) {
    case "object":
      return !isEmpty(value);
    case "number":
      return value > 0;
    default:
      return true;
  }
};

/**
 * Creates a label for a FilteredBy item
 */
const getFilterLabel = (filter, translations) => {
  const key = filter[0];
  const value = filter[1];
  const {
    category,
    text,
    km,
    required_data,
    employees,
    apps,
    changedsince
  } = translations;

  const { values } = FilterAgeDefaultProps;

  switch (key) {
    case "category":
      return `${category}: ${capitalize(value)}`;
    case "text":
      return `${text}: ${value}`;
    case "km":
      return `${km}: ${value}`;
    case "required_data":
      return `${required_data}: ${capitalize(value)}`;
    case "from":
    case "to":
      return `${employees} ${key}: ${value}`;
    case "apps":
      return `${apps}: ${capitalize(value)}`;
    case "changedSince":
      return `${changedsince}: ${moment(value).format("LL")}`;
    case "age":
      return `${capitalize(key)}: ${values
        .filter(age => age.value === value)
        .map(age => age.name)}`;
    default:
      return `${capitalize(key)}: ${capitalize(value)}`;
  }
};

/**
 * Displays the component
 */
const FilteredBy = props => {
  const { filters, setFilters } = useContext(FilterContext);
  const { country } = filters;
  const filtersMap = Object.entries(filters);

  const { t } = useTranslation("FilteredBy");
  const classes = useStyles();

  /**
   * Match filter names with filter display values
   *
   * - A filter name in the API (`category`) can differ from the filter display name (`Industry`)
   *
   * // TODO: These translations are duplicated in the respective filters ...
   */
  const translations = {
    category: t("Industry"),
    text: t("Name/keywords"),
    km: t("Location range"),
    required_data: t("Available data"),
    employees: t("Employees"),
    apps: t("Website technique"),
    changedsince: t("Changed since")
  };

  /**
   * Handles the delete actions
   *
   * - The country filter cannot be removed
   */
  const handleDelete = key => () => {
    if (key === "country") return;

    setFilters(prevState => {
      return { ...prevState, [key]: null };
    });
  };

  const handleDeleteAll = () => {
    setFilters({ country: country });
  };

  /**
   * Prepares the items
   */
  const items =
    (filtersMap &&
      filtersMap
        .filter(filter => isFilterSet(filter))
        .filter(filter => !["country", "page", "rows"].includes(filter[0]))
        .map(filter => {
          const key = filter[0];
          const label = getFilterLabel(filter, translations);
          return (
            <Chip
              key={key}
              label={label}
              onDelete={handleDelete(key)}
              className={classes.chip}
            />
          );
        })) ||
    [];

  return (
    <Grid item className="FilteredBy">
      <FormControl className={classes.formControl}>
        <div className={classes.chips}>
          <Chip
            key="clear-all"
            label={t("Clear All Filters")}
            onDelete={handleDeleteAll}
          />
          {items}
        </div>
      </FormControl>
    </Grid>
  );
};

FilteredBy.propTypes = propTypes;
FilteredBy.defaultProps = defaultProps;

export default FilteredBy;
export {
  propTypes as FilteredByPropTypes,
  defaultProps as FilteredByDefaultProps
};
