/**
 * Displays a company in a list view
 */
import React, { useState } from "react";
import PropTypes from "prop-types";

import CompanyPopup, {
  CompanyPopupPropTypes,
  CompanyPopupDefaultProps
} from "../CompanyPopup";
import {
  companyGetLogo,
  companyGetRangeAndIcon,
  companyGetContactIconsFromDataExists
} from "../Company";

import Grid from "@material-ui/core/Grid";
import Dialog from "@material-ui/core/Dialog";
import { makeStyles } from "@material-ui/core/styles";

import i18n from "../../../i18n";
import { useTranslation } from "react-i18next";
import { en_en } from "./CompanyList.lang.en-en";
import { nl_nl } from "./CompanyList.lang.nl-nl";
import { nl_be } from "./CompanyList.lang.nl-be";

i18n.addResourceBundle("en-EN", "CompanyList", en_en);
i18n.addResourceBundle("nl-NL", "CompanyList", nl_nl);
i18n.addResourceBundle("nl-BE", "CompanyList", nl_be);

/**
 * Defines the prop types
 *
 * @see http://api.finsterdata.com/v1/companies/?country=gb&data_exists=url&action=Gridst&facets=true&token=d11e7916590604d1356a08579aad99dc6bd3cfd1
 */
const propTypes = {
  companyPopup: PropTypes.shape(CompanyPopupPropTypes),
  isVisited: PropTypes.bool,
  setVisited: PropTypes.func
};

/**
 * Defines the Default props
 */
const defaultProps = {
  companyPopup: CompanyPopupDefaultProps,
  isVisited: false,
  setVisited: () => console.log("setVisited")
};

/**
 * Styles the component
 */
const useStyles = makeStyles(theme => ({
  root: {
    "& .visited": {
      color: "gray"
    }
  }
}));

/**
 * Displays the component
 */
const CompanyList = props => {
  const { companyPopup } = props;
  const { company } = companyPopup;
  const { name, city, employees_range, revenue_range } = company;

  //const theme = useTheme();
  const { t } = useTranslation("Company");
  const classes = useStyles();

  const { isVisited, setVisited } = props;
  const visitedClassName = isVisited ? "visited" : "";

  /**
   * Collects opened / visited companies during a popup prev/next navigation session
   */
  const [opened, setOpened] = useState("");

  /**
   * Manages the modal state
   */
  const [open, setOpen] = useState(false);

  const handleClickOpen = () => {
    setOpen(true);
  };

  const handleClose = () => {
    setOpen(false);
    setVisited(prevState => `${opened},${prevState}`);
  };

  return (
    <Grid item xs={12} className={`${classes.root} CompanyList`}>
      <Grid
        container
        spacing={4}
        onClick={handleClickOpen}
        className={`ListItem ${visitedClassName}`}
      >
        <Grid item>
          <img src={companyGetLogo(company)} alt={name} />
        </Grid>
        <Grid item>{name}</Grid>
        <Grid item>{city}</Grid>
        <Grid item>{companyGetRangeAndIcon(employees_range, "employees")}</Grid>
        <Grid item>{companyGetRangeAndIcon(revenue_range, "revenue")}</Grid>
        <Grid item>
          {companyGetContactIconsFromDataExists({
            ...company,
            available: t("available")
          })}
        </Grid>
      </Grid>
      <Dialog
        open={open}
        onClose={handleClose}
        aria-labelledby="form-dialog-title"
      >
        <CompanyPopup {...companyPopup} setOpened={setOpened} />
      </Dialog>
    </Grid>
  );
};

CompanyList.propTypes = propTypes;
CompanyList.defaultProps = defaultProps;

export default CompanyList;
export {
  propTypes as CompanyListPropTypes,
  defaultProps as CompanyListDefaultProps
};
