/**
 * Displays the Changed Since filter
 */
import React, { useContext } from "react";
import PropTypes from "prop-types";
import moment from "moment";

import { FilterContext } from "../Search";

import { makeStyles } from "@material-ui/core/styles";
import FormControl from "@material-ui/core/FormControl";
import Grid from "@material-ui/core/Grid";

import DateFnsUtils from "@date-io/date-fns";
import {
  MuiPickersUtilsProvider,
  KeyboardDatePicker
} from "@material-ui/pickers";

import i18n from "../../../i18n";
import { useTranslation } from "react-i18next";
import { en_en } from "./FilterChangedSince.lang.en-en";
import { nl_nl } from "./FilterChangedSince.lang.nl-nl";
import { nl_be } from "./FilterChangedSince.lang.nl-be";

i18n.addResourceBundle("en-EN", "FilterChangedSince", en_en);
i18n.addResourceBundle("nl-NL", "FilterChangedSince", nl_nl);
i18n.addResourceBundle("nl-BE", "FilterChangedSince", nl_be);

/**
 * Defines the prop types
 */
const propTypes = {
  // TODO: Get a proper proptype here from airbnb
  defaultValue: PropTypes.any
};

/**
 * Defines the default props
 */
const defaultProps = {
  defaultValue: new Date()
};

/**
 * Styles the component
 */
const useStyles = makeStyles(theme => ({
  formControl: {
    margin: theme.spacing(1),
    minWidth: 120,
    maxWidth: 300,
    width: "100%"
  }
}));

/**
 * Displays the component
 */
const FilterChangedSince = props => {
  const { defaultValue } = props;

  const { filters, setFilters } = useContext(FilterContext);
  const changedSince = filters?.changedSince || defaultValue;

  const { t } = useTranslation("FilterChangedSince");
  const classes = useStyles();

  /**
   * Handles the input change
   */
  const inputHandler = date => {
    setFilters(prevState => {
      return { ...prevState, changedSince: moment(date).toISOString() };
    });
  };

  return (
    <Grid item xs={12} className="FilterChangedSince">
      <FormControl className={classes.formControl}>
        <MuiPickersUtilsProvider utils={DateFnsUtils}>
          <KeyboardDatePicker
            disableToolbar
            variant="inline"
            format="MMMM d, yyyy" // TODO: can't get localization to work here ....
            margin="normal"
            name="changedSince"
            label={t("Changed since")}
            value={changedSince}
            onChange={inputHandler}
            KeyboardButtonProps={{
              "aria-label": "change defaultValue"
            }}
          />
        </MuiPickersUtilsProvider>
      </FormControl>
    </Grid>
  );
};

FilterChangedSince.propTypes = propTypes;
FilterChangedSince.defaultProps = defaultProps;

export default FilterChangedSince;
export {
  propTypes as FilterChangedSincePropTypes,
  defaultProps as FilterChangedSinceDefaultProps
};
