import i18n from "i18next";
import { initReactI18next } from "react-i18next";
import LanguageDetector from "i18next-browser-languagedetector";
import moment from "moment";
import numeral from "numeral";
import "numeral/locales/en-gb";
import "numeral/locales/nl-nl";
import "numeral/locales/nl-be";

/**
 * Sets up translations
 *
 * - Every component manages it's own translations via namespaces
 * - Every component adds it's own translation items to `resources`
 *
 * @see https://github.com/i18next/react-i18next/issues/299
 * @see https://react.i18next.com/latest/usetranslation-hook#loading-namespaces
 *
 */
const resources = {};

/**
 * Sets up i18next
 *
 * @see https://react.i18next.com/latest/using-with-hooks
 */
i18n
  /**
   * Detects user language
   *
   * @see https://github.com/i18next/i18next-browser-languageDetector
   */
  .use(LanguageDetector)
  /**
   * Passes the i18n instance to react-i18next.
   */
  .use(initReactI18next)
  /**
   * Inits i18next
   *
   * @see https://www.i18next.com/overview/configuration-options
   */
  .init({
    resources,
    lng: "en-EN",
    debug: false,
    fallbackLng: "en-EN",
    keySeparator: false, // we do not use keys in form messages.welcome
    interpolation: {
      escapeValue: false, // react already safes from xss
      /**
       * Localizing numbers and dates
       *
       * - How it works:
       *   - in code: t("results_total_prefix", { number: total })
       *   - in the language file: results_total_prefix: "Filter results: {{number, decimal}}"
       *
       * - https://www.i18next.com/translation-function/formatting
       * - https://github.com/i18next/react-i18next/issues/738
       */
      format: function(value, format, lng) {
        //console.log("Value: " + value + " Format: " + format + " Lng: " + lng);
        if (value instanceof Date) {
          return moment(value).format(format);
        }
        if (format === "decimal") {
          return numeral(value).format("0,0");
        }
        return numeral(value).format(format);
      }
    },
    load: "currentOnly",
    react: {
      // ensure all required language files are loaded before rendering
      wait: true
    }
  });

i18n.on("languageChanged", function(lng) {
  moment.locale(lng);
  numeral.locale(lng); // TODO: It will fail if there is no language file (like 'en-EN')
});

export default i18n;
