/**
 * Displays the Industry filter
 */
import React, { useContext } from "react";
import PropTypes from "prop-types";

import { FilterContext } from "../Search";
import { FacetsPropTypes, FacetsDefaultProps } from "../Facets";

import { makeStyles } from "@material-ui/core/styles";
import Grid from "@material-ui/core/Grid";
import FormControl from "@material-ui/core/FormControl";
import Autocomplete from "@material-ui/lab/Autocomplete";
import TextField from "@material-ui/core/TextField";
import Chip from "@material-ui/core/Chip";

import i18n from "../../../i18n";
import { useTranslation } from "react-i18next";
import { en_en } from "./FilterIndustry.lang.en-en";
import { nl_nl } from "./FilterIndustry.lang.nl-nl";
import { nl_be } from "./FilterIndustry.lang.nl-be";

i18n.addResourceBundle("en-EN", "FilterIndustry", en_en);
i18n.addResourceBundle("nl-NL", "FilterIndustry", nl_nl);
i18n.addResourceBundle("nl-BE", "FilterIndustry", nl_be);

/**
 * Defines the prop types
 *
 * @see http://api.finsterdata.com/api/companies/?country=gb&facets=industry&rows=0
 * // TODO: Works without the token
 */
const propTypes = {
  industries: PropTypes.shape(FacetsPropTypes),
  selected: PropTypes.arrayOf(PropTypes.string)
};

/**
 * Defines the default props
 */
const defaultProps = {
  industries: FacetsDefaultProps,
  selected: []
};

/**
 * Styles the component
 */
const useStyles = makeStyles(theme => ({
  formControl: {
    margin: theme.spacing(1),
    minWidth: 120,
    maxWidth: 300,
    width: "100%",
    "& .MuiChip-root": {
      margin: 2,
      padding: 4,
      // Wrap long text
      height: "auto",
      "& .MuiChip-label": {
        overflowWrap: "break-word",
        whiteSpace: "normal",
        lineHeight: "1"
      }
    }
  }
}));

/**
 * Displays the component
 */
const FilterIndustry = props => {
  const { industries: defaultIndustries, selected: defaultSelected } = props;

  const { t } = useTranslation("FilterIndustry");
  const classes = useStyles();

  /**
   * Loads the selectbox values from the context
   *
   * // TODO: this returns the industries facet: http://api.finsterdata.com/v1/companies?facets=true&country=GB&token=d11e7916590604d1356a08579aad99dc6bd3cfd1 and this DOESN't http://api.finsterdata.com/v1/companies?action=list&facets=true&country=GB&token=d11e7916590604d1356a08579aad99dc6bd3cfd1
   */
  const { facets } = useContext(FilterContext);
  const industries =
    facets.industry && facets.industry.buckets
      ? facets.industry.buckets
      : defaultIndustries.list;

  /**
   * Handles the filters
   */
  const { filters, setFilters } = useContext(FilterContext);
  const selected = filters?.category || defaultSelected;

  /**
   * Handles the select change
   *
   * // BUG: After the selected items are cleared in `FilteredBy` the `value` is never cleared
   */
  const selectHandler = (event, value) => {
    const values = value.map(item => item.val);
    const lastValue = values.pop();
    const bugFixedValues = [...selected, lastValue];

    setFilters(prevState => {
      return { ...prevState, category: bugFixedValues };
    });
  };

  // TODO: On country change always 100 industries are returned, is this ok?
  // TODO: If the country is changed should selected industries emptied?
  //console.log("c:", results.length);

  /**
   * How it works:
   *
   * - filterSelectedOptions: has to be set, otherwise an option can be selected twice
   * - closeIcon: has to be removed since <Autocomplete/> has an internal state for selected items not matching our own selected state. Emptying with `closeIcon` won't empty our `selected` items.
   * - renderTags: they have to be overwritten to display items from our `selected` instead of the component's own selected
   * - with renderTags overwritten we have a custom Chip component where the onDelete event passes just one param which unusable to find which item to delete. Same true for onClick. So the event has to be handled inside the Chip cannot be moved out into a function
   */
  return (
    <Grid item xs={12} className="FilterIndustry">
      <FormControl className={classes.formControl}>
        <Autocomplete
          multiple
          filterSelectedOptions
          name="industry"
          options={industries}
          getOptionLabel={option => option.val}
          onChange={selectHandler}
          closeIcon={null}
          renderInput={params => (
            <TextField {...params} variant="standard" label={t("Industry")} />
          )}
          renderTags={() => (
            <div className={classes.chips}>
              {selected.map(value => (
                <Chip
                  size="small"
                  key={value}
                  label={value}
                  className={classes.chip}
                  onDelete={() => {
                    setFilters(prevState => {
                      return {
                        ...prevState,
                        category: selected.filter(item => item !== value)
                      };
                    });
                  }}
                />
              ))}
            </div>
          )}
        />
      </FormControl>
    </Grid>
  );
};

FilterIndustry.propTypes = propTypes;
FilterIndustry.defaultProps = defaultProps;

export default FilterIndustry;
export {
  propTypes as FilterIndustryPropTypes,
  defaultProps as FilterIndustryDefaultProps
};
