/**
 * Displays the Location filter
 */
import React, { useState, useContext, useRef } from "react";
import PropTypes from "prop-types";
import { debounce } from "lodash";
import shortid from "shortid";

import { FilterContext } from "../Search";

import { makeStyles } from "@material-ui/core/styles";
import TextField from "@material-ui/core/TextField";
import MenuItem from "@material-ui/core/MenuItem";
import FormControl from "@material-ui/core/FormControl";
import Select from "@material-ui/core/Select";
import Grid from "@material-ui/core/Grid";

import i18n from "../../../i18n";
import { useTranslation } from "react-i18next";
import { en_en } from "./FilterLocation.lang.en-en";
import { nl_nl } from "./FilterLocation.lang.nl-nl";
import { nl_be } from "./FilterLocation.lang.nl-be";

i18n.addResourceBundle("en-EN", "FilterLocation", en_en);
i18n.addResourceBundle("nl-NL", "FilterLocation", nl_nl);
i18n.addResourceBundle("nl-BE", "FilterLocation", nl_be);

/**
 * Defines the prop types
 *
 * @see FilterLocation.md for details
 */
const propTypes = {
  /**
   * Defines the type of the query
   */
  queryType: PropTypes.oneOf(["location", "geo"]),
  /**
   * Defines the query default value
   */
  defaultValue: PropTypes.string,
  /**
   * Defines the values of the radius dropdown list
   */
  radiusList: PropTypes.arrayOf(PropTypes.string)
};

/**
 * Defines the default props
 */
const defaultProps = {
  queryType: "location",
  defaultValue: "",
  radiusList: ["+ 5km", "+ 10km", "+ 15km", "+ 30km", "+ 50km", "+ 100km"]
};

/**
 * Styles the component
 */
const useStyles = makeStyles(theme => ({
  formControl: {
    margin: theme.spacing(1),
    minWidth: 120,
    maxWidth: 300,
    width: "100%",
    display: "flex",
    flexDirection: "row",
    flexWrap: "nowrap",
    alignItems: "flex-end"
  },
  textField: {
    flexGrow: "3"
  },
  select: {
    flexGrow: "1"
  }
}));

/**
 * Displays the component
 */
const FilterLocation = props => {
  const { radiusList, defaultValue } = props;
  const defaultRadius = radiusList[0];

  const { filters, setFilters } = useContext(FilterContext);
  const defaultLocation = filters?.location || defaultValue;
  const radius = filters?.km || defaultRadius;

  const { t } = useTranslation("FilterLocation");
  const label = t("Label");

  const classes = useStyles();

  /**
   * Manages the state of the input
   */
  const [location, setLocation] = useState(defaultLocation);

  /**
   * Updates the location filter
   */
  const updateLocationFilter = value => {
    setFilters(prevState => {
      return { ...prevState, location: value };
    });
  };

  /**
   * Debounces the filter update
   *
   * @see https://medium.com/@rajeshnaroth/using-throttle-and-debounce-in-a-react-function-component-5489fc3461b3
   */
  const updateLocationFilterWithDebounce = useRef(
    debounce(value => updateLocationFilter(value), 500)
  ).current;

  /**
   * Handles the location input change
   */
  const inputHandler = event => {
    // TODO: Logic needed here to determine the query param (location or geo)
    setLocation(event.target.value);
    updateLocationFilterWithDebounce(event.target.value);
  };

  /**
   * Handles the select change
   */
  const selectHandler = data => {
    setFilters(prevState => {
      return { ...prevState, km: data.target.value };
    });
  };

  const radiusItems = radiusList.map(item => (
    <MenuItem key={shortid.generate()} value={item}>
      {item}
    </MenuItem>
  ));

  return (
    <Grid item xs={12} className="FilterLocation">
      <FormControl className={classes.formControl}>
        <TextField
          name="location"
          className={classes.textField}
          type="search"
          label={label}
          value={location}
          onChange={inputHandler}
        />
        <Select
          name="km"
          className={classes.select}
          value={radius}
          onChange={selectHandler}
        >
          {radiusItems}
        </Select>
      </FormControl>
    </Grid>
  );
};

FilterLocation.propTypes = propTypes;
FilterLocation.defaultProps = defaultProps;

export default FilterLocation;
export {
  propTypes as FilterLocationPropTypes,
  defaultProps as FilterLocationDefaultProps
};
