import React from 'react';
import PropTypes from 'prop-types';

import i18n from '../../i18n';
import { en_en } from './InputNumber.lang.en-en';
import { nl_nl } from './InputNumber.lang.nl-nl';
import { nl_be } from './InputNumber.lang.nl-be';
import { TextField } from '@material-ui/core';

i18n.addResourceBundle('en-EN', 'InputNumber', en_en);
i18n.addResourceBundle('nl-NL', 'InputNumber', nl_nl);
i18n.addResourceBundle('nl-BE', 'InputNumber', nl_be);

/**
 * Defines the prop types
 */
const propTypes = {
  error: PropTypes.string,
  label: PropTypes.string,
  name: PropTypes.string,
  onChange: PropTypes.func,
  value: PropTypes.string,
  variant: PropTypes.string,
  required: PropTypes.bool
};

/**
 * Defines the default props
 */
const defaultProps = {
  error: null,
  label: '',
  name: 'textInput',
  onChange: () => {},
  value: undefined,
  variant: 'outlined',
  required: false
};

/**
 * Displays the component
 */
const InputNumber = props => {
  const { label, variant, onChange, name, error, required, ...rest } = props;
  return (
    <TextField
      name={name}
      label={label}
      variant={variant}
      error={error ? true : false}
      helperText={error}
      type='number'
      onChange={onChange}
      required={required}
      {...rest}
    />
  );
};

InputNumber.propTypes = propTypes;
InputNumber.defaultProps = defaultProps;

export default InputNumber;
export {
  propTypes as InputNumberPropTypes,
  defaultProps as InputNumberDefaultProps
};
