import React, { Suspense } from "react";
import PropTypes from "prop-types";
import { AuthProvider } from "../../hooks";
import { GoogleReCaptchaProvider } from "react-google-recaptcha-v3";
import theme from "../../theme";
import { ThemeProvider } from "@material-ui/core";
import { SnackbarProvider } from "notistack";

/**
 * Defines the prop types
 */
const propTypes = {};

/**
 * Defines the default props
 */
const defaultProps = {};

/**
 * Displays the component
 */
const Providers = props => {
  const { children } = props;
  return (
    <Suspense fallback={<div>Loading</div>}>
      {/* // TODO:  Suspense usage is too vague. Please remove. */}
      <ThemeProvider theme={theme}>
        <SnackbarProvider
          anchorOrigin={{
            vertical: "top",
            horizontal: "right"
          }}
          maxSnack={3}
        >
          {/* // TODO: This is not required for lots of pages. Please add only where needed. */}
          {/* <GoogleReCaptchaProvider reCaptchaKey='6Lef3MEUAAAAAEBgmgSa4i6a4Napq2iD32qS4DrG'> */}
          <AuthProvider strategy="finster">{children}</AuthProvider>
          {/* </GoogleReCaptchaProvider> */}
        </SnackbarProvider>
      </ThemeProvider>
    </Suspense>
  );
};

Providers.propTypes = propTypes;
Providers.defaultProps = defaultProps;

export default Providers;
export {
  propTypes as ProvidersPropTypes,
  defaultProps as ProvidersDefaultProps
};
