import React, { useContext } from "react";
import PropTypes from "prop-types";
import shortid from "shortid";

import { FilterContext } from "../Search";

import { makeStyles } from "@material-ui/core/styles";
import InputLabel from "@material-ui/core/InputLabel";
import MenuItem from "@material-ui/core/MenuItem";
import FormControl from "@material-ui/core/FormControl";
import Select from "@material-ui/core/Select";
import Grid from "@material-ui/core/Grid";

import i18n from "../../../i18n";
import { useTranslation } from "react-i18next";
import { en_en } from "./PaginationRowsPerPage.lang.en-en";
import { nl_nl } from "./PaginationRowsPerPage.lang.nl-nl";
import { nl_be } from "./PaginationRowsPerPage.lang.nl-be";

i18n.addResourceBundle("en-EN", "PaginationRowsPerPage", en_en);
i18n.addResourceBundle("nl-NL", "PaginationRowsPerPage", nl_nl);
i18n.addResourceBundle("nl-BE", "PaginationRowsPerPage", nl_be);

/**
 * Defines the prop types
 */
const propTypes = {
  values: PropTypes.arrayOf(PropTypes.number)
};

/**
 * Defines the default props
 *
 * // TODO: Get the right values here ... for 500 the query breaks the brower
 */
const defaultProps = {
  values: [10, 20, 50, 100]
};

/**
 * Styles the component
 */
const useStyles = makeStyles(theme => ({
  formControl: {
    margin: theme.spacing(1),
    minWidth: 120,
    maxWidth: 300,
    width: "100%"
  }
}));

/**
 * Displays the component
 */
const PaginationRowsPerPage = props => {
  const { values } = props;

  const { filters, setFilters } = useContext(FilterContext);
  const rows = filters?.rows || values[0];

  const { t } = useTranslation("PaginationRowsPerPage");
  const classes = useStyles();

  /**
   * Handles the select change
   */
  const selectHandler = data => {
    setFilters(prevState => {
      return { ...prevState, rows: data.target.value };
    });
  };

  /**
   * Prepares the items
   */
  const items = values.map(value => {
    return (
      <MenuItem key={shortid.generate()} value={value}>
        {value}
      </MenuItem>
    );
  });

  return (
    <Grid item xs={12} className="PaginationRowsPerPage">
      <FormControl className={classes.formControl}>
        <InputLabel>{t("PaginationRowsPerPage")}</InputLabel>
        <Select name="rowsperpage" value={rows} onChange={selectHandler}>
          {items}
        </Select>
      </FormControl>
    </Grid>
  );
};

PaginationRowsPerPage.propTypes = propTypes;
PaginationRowsPerPage.defaultProps = defaultProps;

export default PaginationRowsPerPage;
export {
  propTypes as PaginationRowsPerPagePropTypes,
  defaultProps as PaginationRowsPerPageDefaultProps
};
