/**
 * - Displays a country
 * - Defines a function to display a country
 */
import React from "react";
import PropTypes from "prop-types";
import shortid from "shortid";

import MenuItem from "@material-ui/core/MenuItem";

import i18n from "../../../i18n";
//import { useTranslation } from "react-i18next";
import { en_en } from "./Country.lang.en-en";
import { nl_nl } from "./Country.lang.nl-nl";
import { nl_be } from "./Country.lang.nl-be";

i18n.addResourceBundle("en-EN", "Country", en_en);
i18n.addResourceBundle("nl-NL", "Country", nl_nl);
i18n.addResourceBundle("nl-BE", "Country", nl_be);

/**
 * Defines the prop types
 */
const propTypes = {
  country_code: PropTypes.string,
  country: PropTypes.string
};

/**
 * Defines the default props
 */
const defaultProps = {
  country_code: "GB",
  country: "United Kingdom"
};

/**
 * Displays the component via a function call
 *
 * - This is the only way to make Material UI's Select to work
 * - No refs can help here accoring to my current knowledge
 *
 * @see FilterCountry.md
 */
const displayCountry = props => {
  const { country_code, country } = props;

  return (
    <MenuItem value={country_code} key={shortid.generate()} className="Country">
      {country}
    </MenuItem>
  );
};

/**
 * Displays the component
 */
const Country = React.forwardRef((props, ref) => {
  const { country_code, country } = props;

  return (
    <MenuItem value={country_code} ref={ref} className="Country">
      {country}
    </MenuItem>
  );
});

Country.propTypes = propTypes;
Country.defaultProps = defaultProps;

export default Country;
export {
  propTypes as CountryPropTypes,
  defaultProps as CountryDefaultProps,
  displayCountry
};
