/**
 * Displays the serach results
 */
import React from "react";
import PropTypes from "prop-types";

import Grid from "@material-ui/core/Grid";

import Companies, {
  CompaniesPropTypes,
  CompaniesDefaultProps
} from "../Companies";
import ResultsDownload, {
  ResultsDownloadPropTypes,
  ResultsDownloadDefaultProps
} from "../ResultsDownload";

import i18n from "../../../i18n";
//import { useTranslation } from "react-i18next";
import { en_en } from "./Results.lang.en-en";
import { nl_nl } from "./Results.lang.nl-nl";
import { nl_be } from "./Results.lang.nl-be";

i18n.addResourceBundle("en-EN", "Results", en_en);
i18n.addResourceBundle("nl-NL", "Results", nl_nl);
i18n.addResourceBundle("nl-BE", "Results", nl_be);

/**
 * Defines the prop types
 */
const propTypes = {
  companies: PropTypes.shape(CompaniesPropTypes),
  download: PropTypes.shape(ResultsDownloadPropTypes)
};

/**
 * Defines the default props
 */
const defaultProps = {
  companies: CompaniesDefaultProps,
  download: ResultsDownloadDefaultProps
};

/**
 * Displays the component
 */
const Results = props => {
  const { companies, download, total } = props;

  return (
    <Grid item className="Results">
      <Grid container spacing={4}>
        <ResultsDownload {...download} />
        <Companies {...companies} total={total} />
      </Grid>
    </Grid>
  );
};

Results.propTypes = propTypes;
Results.defaultProps = defaultProps;

export default Results;
export { propTypes as ResultsPropTypes, defaultProps as ResultsDefaultProps };
