/**
 * Displays the Search / Text filter
 */
import React, { useState, useContext, useRef } from "react";
import PropTypes from "prop-types";
import { debounce } from "lodash";

import { FilterContext } from "../Search";

import { makeStyles } from "@material-ui/core/styles";
import TextField from "@material-ui/core/TextField";
import FormControl from "@material-ui/core/FormControl";
import Grid from "@material-ui/core/Grid";

import i18n from "../../../i18n";
import { useTranslation } from "react-i18next";
import { en_en } from "./FilterSearch.lang.en-en";
import { nl_nl } from "./FilterSearch.lang.nl-nl";
import { nl_be } from "./FilterSearch.lang.nl-be";

i18n.addResourceBundle("en-EN", "FilterSearch", en_en);
i18n.addResourceBundle("nl-NL", "FilterSearch", nl_nl);
i18n.addResourceBundle("nl-BE", "FilterSearch", nl_be);

/**
 * Defines the prop types
 *
 * @see http://api.finsterdata.com/v1/companies/?country=gb&text=abn%20amro&facets=true&token=d11e7916590604d1356a08579aad99dc6bd3cfd1
 */
const propTypes = {
  defaultValue: PropTypes.string
};

/**
 * Defines the default props
 */
const defaultProps = {
  defaultValue: ""
};

/**
 * Styles the component
 */
const useStyles = makeStyles(theme => ({
  formControl: {
    margin: theme.spacing(1),
    minWidth: 120,
    maxWidth: 300,
    width: "100%"
  }
}));

/**
 * Displays the component
 */
const FilterSearch = props => {
  const { defaultValue } = props;
  const { filters, setFilters } = useContext(FilterContext);
  const defaulText = filters?.text || defaultValue;

  const { t } = useTranslation("FilterSearch");
  const label = t("Label");

  const classes = useStyles();

  /**
   * Manages the state of the input
   */
  const [text, setText] = useState(defaulText);

  /**
   * Updates the filters
   */
  const updateFilters = value => {
    setFilters(prevState => {
      return { ...prevState, text: value };
    });
  };

  /**
   * Debounces the filter update
   *
   * @see https://medium.com/@rajeshnaroth/using-throttle-and-debounce-in-a-react-function-component-5489fc3461b3
   */
  const updateFiltersWithDebounce = useRef(
    debounce(value => {
      updateFilters(value);
    }, 500)
  ).current;

  /**
   * Handles the input change
   */
  const inputHandler = event => {
    setText(event.target.value);
    updateFiltersWithDebounce(event.target.value);
  };

  return (
    <Grid item xs={12} className="FilterSearch">
      <FormControl className={classes.formControl}>
        <TextField
          type="search"
          name="search"
          label={label}
          value={text}
          onChange={inputHandler}
        />
      </FormControl>
    </Grid>
  );
};

FilterSearch.propTypes = propTypes;
FilterSearch.defaultProps = defaultProps;

export default FilterSearch;
export {
  propTypes as FilterSearchPropTypes,
  defaultProps as FilterSearchDefaultProps
};
