/**
 * Displays a facet
 *
 * - For now it just holds the PropTypes
 */
import React from "react";
import PropTypes from "prop-types";

import i18n from "../../../i18n";
import { useTranslation } from "react-i18next";
import { en_en } from "./Facet.lang.en-en";
import { nl_nl } from "./Facet.lang.nl-nl";
import { nl_be } from "./Facet.lang.nl-be";

i18n.addResourceBundle("en-EN", "Facet", en_en);
i18n.addResourceBundle("nl-NL", "Facet", nl_nl);
i18n.addResourceBundle("nl-BE", "Facet", nl_be);

/**
 * Defines the prop types
 */
const propTypes = PropTypes.shape({
  val: PropTypes.string,
  count: PropTypes.number
});

/**
 * Defines the default props
 */
const defaultProps = {
  val: "No data found.",
  count: 0
};

/**
 * Displays the component
 */
const Facet = props => {
  const { t } = useTranslation("Facet");
  return <div className="Facet">{t("Facet")}</div>;
};

Facet.propTypes = propTypes;
Facet.defaultProps = defaultProps;

export default Facet;
export { propTypes as FacetPropTypes, defaultProps as FacetDefaultProps };
