import React from "react";
import PropTypes from "prop-types";

import {
  BrowserRouter as Router,
  Route,
  useLocation,
  Switch
} from "react-router-dom";

import Home from "../_home/Home";
import Search from "../_search/Search";
import CompanyPopup from "../_search/CompanyPopup";
import LanguageSelector from "../LanguageSelector";

/**
 * Defines the prop types
 */
const propTypes = {};

/**
 * Defines the default props
 */
const defaultProps = {};

/**
 * Displays a company if an `fd` query param is present
 *
 * Ex.: http://localhost:3000/?fd=NL12345678 or finsterdata.com?fd=NL12345678
 */
const CompanyByFD = () => {
  const query = new URLSearchParams(useLocation().search);
  return query.has("fd") ? <CompanyPopup fd={query.get("fd")} /> : <Home />;
};

/**
 * Displays the component
 */
const Routes = props => {
  const { login, account, history, register } = props;
  return (
    <Router>
      <Switch>
        <Route exact path="/search">
          <LanguageSelector />
          <Search />
        </Route>
        <Route path="/">
          <CompanyByFD />
        </Route>
      </Switch>
    </Router>
  );
};

Routes.propTypes = propTypes;
Routes.defaultProps = defaultProps;

export default Routes;
export { propTypes as RoutesPropTypes, defaultProps as RoutesDefaultProps };
