import React from "react";
import PropTypes from "prop-types";
import shortid from "shortid";

import Plan, { PlanPropTypes } from "../Plan";

import Grid from "@material-ui/core/Grid";
import { makeStyles } from "@material-ui/core/styles";

import i18n from "../../../i18n";
//import { useTranslation } from "react-i18next";
import { en_en } from "./Plans.lang.en-en";
import { nl_nl } from "./Plans.lang.nl-nl";
import { nl_be } from "./Plans.lang.nl-be";

i18n.addResourceBundle("en-EN", "Plans", en_en);
i18n.addResourceBundle("nl-NL", "Plans", nl_nl);
i18n.addResourceBundle("nl-BE", "Plans", nl_be);

/**
 * Defines the prop types
 */
const propTypes = {
  plans: PropTypes.arrayOf(PropTypes.shape(PlanPropTypes))
};

/**
 * Defines the default props
 */
const defaultProps = {
  plans: [
    {
      title: "Bronze",
      price: "$ 20",
      features: ["500 Credits", "1 User"]
    },
    {
      title: "Silver",
      price: "$ 40",
      features: ["1000 Credits", "2 Users"],
      ribbon: "Most popular",
      size: "large"
    },
    {
      title: "Gold",
      price: "$ 70",
      features: ["2500 Credits", "4 Users", "API link"]
    }
  ]
};

/**
 * Styles the component
 */
const useStyles = makeStyles(theme => ({
  container: {
    /**
     * Resetting the horizontal scroll
     */
    width: "auto",
    margin: 0
  }
}));

/**
 * Displays the component
 */
const Plans = props => {
  const { plans } = props;

  const classes = useStyles();
  const { container } = classes;

  const cards = plans.map(plan => (
    <Grid item key={shortid.generate()}>
      <Plan {...plan} />
    </Grid>
  ));

  return (
    <Grid
      container
      className={container}
      justify="center"
      alignItems="center"
      spacing={2} // This is a must for tablet portrait
    >
      {cards}
    </Grid>
  );
};

Plans.propTypes = propTypes;
Plans.defaultProps = defaultProps;

export default Plans;
export { propTypes as PlansPropTypes, defaultProps as PlansDefaultProps };
