/**
 * Displays the Available Data filter
 */
import React, { useState, useContext, useEffect } from "react";
import PropTypes from "prop-types";
import shortid from "shortid";
import { capitalize } from "lodash";

import { FilterContext } from "../Search";

import { makeStyles } from "@material-ui/core/styles";
import FormLabel from "@material-ui/core/FormLabel";
import FormControl from "@material-ui/core/FormControl";
import FormGroup from "@material-ui/core/FormGroup";
import FormControlLabel from "@material-ui/core/FormControlLabel";
import Checkbox from "@material-ui/core/Checkbox";
import Grid from "@material-ui/core/Grid";

import i18n from "../../../i18n";
import { useTranslation } from "react-i18next";
import { en_en } from "./FilterAvailableData.lang.en-en";
import { nl_nl } from "./FilterAvailableData.lang.nl-nl";
import { nl_be } from "./FilterAvailableData.lang.nl-be";

i18n.addResourceBundle("en-EN", "FilterAvailableData", en_en);
i18n.addResourceBundle("nl-NL", "FilterAvailableData", nl_nl);
i18n.addResourceBundle("nl-BE", "FilterAvailableData", nl_be);

/**
 * Defines the prop types
 */
const propTypes = {
  values: PropTypes.arrayOf(PropTypes.string)
};

/**
 * Defines the default props
 */
const defaultProps = {
  values: ["phone", "website", "email", "address"]
};

/**
 * Styles the component
 */
const useStyles = makeStyles(theme => ({
  root: {
    display: "flex"
  },
  formControl: {
    margin: theme.spacing(1)
  }
}));

/**
 * Displays the component
 */
const FilterAvailableData = props => {
  const { values } = props;

  const { t } = useTranslation("FilterAvailableData");
  const classes = useStyles();

  /**
   * Handles the filters
   */
  const { filters, setFilters } = useContext(FilterContext);
  const required_data = filters?.required_data || [];

  /**
   * Handles the checbox states
   */
  const [checked, setChecked] = useState(required_data);

  /**
   *
   * The two values are different ... I mean if `required_data` is changed the `checked` state is not updated
   *
   * console.log("required_data:", required_data);
   * console.log("checked:", checked);
   * */

  /**
   * Handles the checkbox change
   */
  const handleChange = event => {
    event.target.checked
      ? setChecked(prevState => {
          return [...prevState, event.target.name];
        })
      : setChecked(prevState => {
          return prevState.filter(item => item !== event.target.name);
        });
  };

  useEffect(() => {
    setFilters(prevState => {
      return {
        ...prevState,
        required_data: checked
      };
    });
  }, [checked, setFilters]);

  /**
   * Prepares the checkboxes
   */
  const items = values.map(item => {
    const isChecked = required_data.includes(item);

    return (
      <FormControlLabel
        key={shortid.generate()}
        control={
          <Checkbox checked={isChecked} onChange={handleChange} name={item} />
        }
        label={capitalize(item)}
      />
    );
  });

  return (
    <Grid item xs={12} className={`FilterAvailableData ${classes.root}`}>
      <FormControl component="fieldset" className={classes.formControl}>
        <FormLabel component="legend">{t("FilterAvailableData")}</FormLabel>
        <FormGroup>{items}</FormGroup>
      </FormControl>
    </Grid>
  );
};

FilterAvailableData.propTypes = propTypes;
FilterAvailableData.defaultProps = defaultProps;

export default FilterAvailableData;
export {
  propTypes as FilterAvailableDataPropTypes,
  defaultProps as FilterAvailableDataDefaultProps
};
