import React, { useRef } from "react";
import PropTypes from "prop-types";

import { makeStyles } from "@material-ui/core/styles";
import FormControl from "@material-ui/core/FormControl";
import FormGroup from "@material-ui/core/FormGroup";
import FormControlLabel from "@material-ui/core/FormControlLabel";
import Checkbox from "@material-ui/core/Checkbox";
import TextField from "@material-ui/core/TextField";

import i18n from "../../../i18n";
import { useTranslation } from "react-i18next";
import { en_en } from "./ResultsDownloadRecords.lang.en-en";
import { nl_nl } from "./ResultsDownloadRecords.lang.nl-nl";
import { nl_be } from "./ResultsDownloadRecords.lang.nl-be";

i18n.addResourceBundle("en-EN", "ResultsDownloadRecords", en_en);
i18n.addResourceBundle("nl-NL", "ResultsDownloadRecords", nl_nl);
i18n.addResourceBundle("nl-BE", "ResultsDownloadRecords", nl_be);

/**
 * Defines the prop types
 */
const propTypes = {
  records: PropTypes.arrayOf(PropTypes.string),
  selected: PropTypes.string,
  all: PropTypes.string,
  top: PropTypes.string,
  otherAmount: PropTypes.number
};

/**
 * Defines the default props
 *
 * // TODO: These strings (top, all) should be also translated
 */
const defaultProps = {
  records: ["top 100", "top 1.000", "top 5.000", "all", "top", "other amount"],
  selected: "top 100",
  all: "",
  top: "",
  otherAmount: 0
};

/**
 * Styles the component
 */
const useStyles = makeStyles(theme => ({
  container: {
    display: "flex",
    flexDirection: "column",
    marginTop: 10,
    paddingTop: 10,
    borderTop: "1px solid",
    marginBottom: 10
  },
  items: {
    display: "flex",
    flexDirection: "row"
  },
  hidden: {
    display: "none"
  }
}));

/**
 * Updates the `all` and `top` records with the `found`, `credits` values
 */
const displayRecords = props => {
  const { records, found, credits, all, top } = props;

  return records.map(record =>
    record === "all"
      ? `${all} ${found}`
      : record === "top"
      ? `${top} ${credits}`
      : record
  );
};

/**
 * Converts a record (`top 100`) to a row query param (`&row=100`)
 */
const convertRecordToRow = props => {
  const { record, otherAmount } = props;

  if (record === "other amount") {
    return otherAmount;
  }

  /**
   * Strip down chars and return only numbers
   */
  const stripped = record.replace(/[^0-9]/g, "");

  return stripped;
};

/**
 * Displays the component
 */
const ResultsDownloadRecords = props => {
  const { record, setRecord, otherAmount, setOtherAmount } = props;

  const { t } = useTranslation("ResultsDownloadRecords");
  const classes = useStyles();

  /**
   * Manages the checkboxes
   */
  const records = displayRecords({ ...props, all: t("all"), top: t("top") });

  const handleCheckboxChange = event => {
    const { target } = event;
    const { value } = target;

    setRecord(value);
  };

  const items = records.map(item => (
    <FormControlLabel
      control={
        <Checkbox
          checked={item === record}
          value={item}
          onChange={handleCheckboxChange}
        />
      }
      label={item}
    />
  ));

  /**
   * Manages the text input
   */
  const inputRef = useRef(null);

  const handleInputChange = event => {
    const value = inputRef.current.value;
    setOtherAmount(value);
  };

  const otherAmountClassName = record === "other amount" ? "" : classes.hidden;

  return (
    <FormControl component="fieldset" className={classes.container}>
      <FormGroup className={classes.items}>{items}</FormGroup>
      <TextField
        type="text"
        name="otherAmount"
        label={t("Other amount")}
        value={otherAmount}
        inputRef={inputRef}
        onChange={handleInputChange}
        className={otherAmountClassName}
      />
    </FormControl>
  );
};

ResultsDownloadRecords.propTypes = propTypes;
ResultsDownloadRecords.defaultProps = defaultProps;

export default ResultsDownloadRecords;
export {
  propTypes as ResultsDownloadRecordsPropTypes,
  defaultProps as ResultsDownloadRecordsDefaultProps,
  convertRecordToRow
};
