import React from "react";
import PropTypes from "prop-types";

import { makeStyles } from "@material-ui/core/styles";
import FormLabel from "@material-ui/core/FormLabel";
import FormControl from "@material-ui/core/FormControl";
import FormGroup from "@material-ui/core/FormGroup";
import FormControlLabel from "@material-ui/core/FormControlLabel";
import Checkbox from "@material-ui/core/Checkbox";

import i18n from "../../../i18n";
import { useTranslation } from "react-i18next";
import { en_en } from "./ResultsDownloadFileTypes.lang.en-en";
import { nl_nl } from "./ResultsDownloadFileTypes.lang.nl-nl";
import { nl_be } from "./ResultsDownloadFileTypes.lang.nl-be";

i18n.addResourceBundle("en-EN", "ResultsDownloadFileTypes", en_en);
i18n.addResourceBundle("nl-NL", "ResultsDownloadFileTypes", nl_nl);
i18n.addResourceBundle("nl-BE", "ResultsDownloadFileTypes", nl_be);

/**
 * Defines the prop types
 */
const propTypes = {
  types: PropTypes.arrayOf(PropTypes.string),
  selected: PropTypes.string
};

/**
 * Defines the default props
 */
const defaultProps = {
  types: ["csv (,)", "csv (;)", "xlsx", "xls", "tsv", "odt"],
  selected: "csv (,)"
};

/**
 * Styles the component
 */
const useStyles = makeStyles(theme => ({
  items: {
    display: "flex",
    flexDirection: "row"
  }
}));

/**
 * Displays the component
 */
const ResultsDownloadFileTypes = props => {
  const { types, fileType, setFileType } = props;

  const { t } = useTranslation("ResultsDownloadFileTypes");
  const classes = useStyles();

  const handleChange = event => {
    const value = event.target.value;
    setFileType(value);
  };

  const items = types.map(item => (
    <FormControlLabel
      control={
        <Checkbox
          checked={item === fileType}
          value={item}
          onChange={handleChange}
        />
      }
      label={item}
    />
  ));

  return (
    <FormControl component="fieldset">
      <FormLabel component="legend">{t("FileTypes")}</FormLabel>
      <FormGroup className={classes.items}>{items}</FormGroup>
    </FormControl>
  );
};

ResultsDownloadFileTypes.propTypes = propTypes;
ResultsDownloadFileTypes.defaultProps = defaultProps;

export default ResultsDownloadFileTypes;
export {
  propTypes as ResultsDownloadFileTypesPropTypes,
  defaultProps as ResultsDownloadFileTypesDefaultProps
};
