/**
 * Displays the Webite Technique filter
 */

import React, { useState, useEffect, useContext } from "react";
import PropTypes from "prop-types";
import shortid from "shortid";

import { useAPI, useAPIPropTypes, mergeApiParams } from "../../../hooks";

import { FilterContext } from "../Search";
import { displayFacets, FacetsPropTypes, FacetsDefaultProps } from "../Facets";

import { makeStyles } from "@material-ui/core/styles";
import Input from "@material-ui/core/Input";
import InputLabel from "@material-ui/core/InputLabel";
import FormControl from "@material-ui/core/FormControl";
import Select from "@material-ui/core/Select";
import Chip from "@material-ui/core/Chip";
import Grid from "@material-ui/core/Grid";

import i18n from "../../../i18n";
import { useTranslation } from "react-i18next";
import { en_en } from "./FilterWebsiteTechnique.lang.en-en";
import { nl_nl } from "./FilterWebsiteTechnique.lang.nl-nl";
import { nl_be } from "./FilterWebsiteTechnique.lang.nl-be";

i18n.addResourceBundle("en-EN", "FilterWebsiteTechnique", en_en);
i18n.addResourceBundle("nl-NL", "FilterWebsiteTechnique", nl_nl);
i18n.addResourceBundle("nl-BE", "FilterWebsiteTechnique", nl_be);

/**
 * Defines the prop types
 *
 * @see http://api.finsterdata.com/v1/companies/?country=nl&facets=apps&rows=0&token=d11e7916590604d1356a08579aad99dc6bd3cfd1
 *
 * @see http://api.finsterdata.com/v1/companies/?country=nl&facets=app_cats&rows=0&token=d11e7916590604d1356a08579aad99dc6bd3cfd1
 */
const propTypes = {
  apps: PropTypes.shape(FacetsPropTypes),
  appsSelected: PropTypes.arrayOf(PropTypes.string),
  appsCat: PropTypes.shape(FacetsPropTypes),
  appsCatSelected: PropTypes.arrayOf(PropTypes.string),
  apiCall: PropTypes.shape(useAPIPropTypes)
};

/**
 * Defines the default props
 */
const defaultProps = {
  apps: FacetsDefaultProps,
  appsSelected: [],
  appsCat: FacetsDefaultProps,
  appsCatSelected: [],
  apiCall: {
    path: {
      endpoint: "companies"
    },
    params: {
      queryParams: {
        facets: "apps,apps_cat",
        rows: "0"
      }
    },
    defaultData: FacetsDefaultProps.list
  }
};

/**
 * Styles the component
 */
const useStyles = makeStyles(theme => ({
  formControl: {
    margin: theme.spacing(1),
    minWidth: 120,
    maxWidth: 300,
    width: "100%"
  },
  chips: {
    display: "flex",
    flexWrap: "wrap"
  },
  chip: {
    margin: 2,
    // Wrap long text
    height: "auto",
    padding: 4,
    "& .MuiChip-label": {
      overflowWrap: "break-word",
      whiteSpace: "normal",
      lineHeight: "1"
    }
  },
  noLabel: {
    marginTop: theme.spacing(3)
  }
}));

/**
 * Displays the component
 */
const FilterWebsiteTechnique = props => {
  const { appsSelected, appsCatSelected, apiCall } = props;
  const { defaultData } = apiCall;

  const { t } = useTranslation("FilterWebsiteTechnique");
  const classes = useStyles();

  /**
   * Handles the filters
   */
  const { filters, setFilters } = useContext(FilterContext);
  const selectedApps = filters?.apps || appsSelected;
  const selectedAppsCat = filters?.apps_cat || appsCatSelected;
  const selected = [...selectedApps, ...selectedAppsCat];

  /**
   * Handles the API call
   */
  const [results, setResults] = useState(defaultData);

  const token = "d11e7916590604d1356a08579aad99dc6bd3cfd1";
  const { country } = filters;

  const params = mergeApiParams({
    requestProps: apiCall,
    requestLiveProps: {
      params: { init: { body: { token: token, country: country } } }
    }
  });

  const { data } = useAPI(params);

  useEffect(() => {
    if (
      data &&
      data.facets &&
      data.facets.apps &&
      data.facets.apps.buckets &&
      data.facets.apps.buckets.length > 0
    ) {
      setResults(data.facets.apps.buckets);
      // TODO: The `apps_cat` case should be covered too
    }
  }, [data]);

  /**
   * Handles the select change
   */
  const selectHandler = data => {
    setFilters(prevState => {
      return { ...prevState, apps: data.target.value };
    });
  };

  return (
    <Grid item xs={12} className="FilterWebsiteTechnique">
      <FormControl className={classes.formControl}>
        <InputLabel>{t("Website technique")}</InputLabel>
        <Select
          multiple
          name="websiteTechnique"
          value={selected}
          onChange={selectHandler}
          input={<Input id="select-multiple-chip" />}
          renderValue={selected => (
            <div className={classes.chips}>
              {selected.map(value => (
                <Chip
                  size="small"
                  key={shortid.generate()}
                  label={value}
                  className={classes.chip}
                />
              ))}
            </div>
          )}
        >
          {displayFacets({ list: results })}
        </Select>
      </FormControl>
    </Grid>
  );
};

FilterWebsiteTechnique.propTypes = propTypes;
FilterWebsiteTechnique.defaultProps = defaultProps;

export default FilterWebsiteTechnique;
export {
  propTypes as FilterWebsiteTechniquePropTypes,
  defaultProps as FilterWebsiteTechniqueDefaultProps
};
