import { useState, useRef, useEffect } from 'react';
import PropTypes from 'prop-types';
import { useAuth } from '../useAuth/useAuth';
import useAPI, {
  mergeApiParams,
  useAPIPropTypes,
  getAPICallStatus
} from '../useAPI';

/**
 * Defines the prop types
 */
const propTypes = {
  pricesCall: PropTypes.shape(useAPIPropTypes),
  subscriptionsCall: PropTypes.shape(useAPIPropTypes)
};

/**
 * Defines the default props
 */
const defaultProps = {
  pricesCall: {
    path: {
      endpoint: 'prices'
    },
    watch: null
  },
  subscriptionsCall: {
    path: {
      endpoint: 'subscriptions'
    },
    watch: null
  }
};

/**
 * Adds the package field to the plans
 */
const parseUpgrades = (upgrades, plans) => {
  return plans.filter(plan => {
    return upgrades.find(upgrade => {
      return plan.plan === upgrade.plan;
    });
  });
};

/**
 * Returns the users active subscription
 */
const getActiveSubscription = subscriptions => {
  if (!subscriptions) {
    return null;
  }

  return subscriptions.find(subscription => {
    return subscription.status === 'active';
  });
};

const useUpgradesData = props => {
  const { pricesCall, subscriptionsCall } = defaultProps;
  const { isAuthenticated } = useAuth();
  const [upgrades, setUpgrades] = useState([]);
  const [activeSubscription, setActiveSubscription] = useState(null);
  const [api, setApi] = useState({});
  const userPlans = useRef();
  // Fetch the prices
  const { data: plansData } = useAPI(
    mergeApiParams({
      requestProps: pricesCall
    })
  );

  // Fetch the subsciptions
  const { data: upgradesData } = useAPI(api);

  useEffect(() => {
    const { successful } = getAPICallStatus(plansData);

    if (successful) {
      userPlans.current = plansData.prices;
      if (isAuthenticated) {
        setApi(
          mergeApiParams({
            requestProps: {
              ...subscriptionsCall
            }
          })
        );
      } else {
        setUpgrades(userPlans.current);
      }
    }
  }, [plansData, isAuthenticated]);

  useEffect(() => {
    const { successful } = getAPICallStatus(upgradesData);

    if (successful && isAuthenticated) {
      if (upgradesData.upgrades) {
        setUpgrades(parseUpgrades(upgradesData.upgrades, userPlans.current));
      }

      if (upgradesData.subscriptions) {
        setActiveSubscription(
          getActiveSubscription(upgradesData.subscriptions)
        );
      }
    } else {
      setUpgrades(userPlans.current);
    }
  }, [upgradesData]);
  return { upgrades, activeSubscription };
};

useUpgradesData.propTypes = propTypes;
useUpgradesData.defaultProps = defaultProps;

export default useUpgradesData;
export {
  propTypes as useUpgradesPropTypes,
  defaultProps as useUpgradesDefaultProps
};
