import React from "react";
import PropTypes from "prop-types";

import Grid from "@material-ui/core/Grid";

import i18n from "../../../i18n";
import { useTranslation } from "react-i18next";
import { en_en } from "./ResultsTotal.lang.en-en";
import { nl_nl } from "./ResultsTotal.lang.nl-nl";
import { nl_be } from "./ResultsTotal.lang.nl-be";

i18n.addResourceBundle("en-EN", "ResultsTotal", en_en);
i18n.addResourceBundle("nl-NL", "ResultsTotal", nl_nl);
i18n.addResourceBundle("nl-BE", "ResultsTotal", nl_be);

/**
 * Defines the prop types
 */
const propTypes = {
  /**
   * The total number of companies found
   */
  total: PropTypes.number
};

/**
 * Defines the default props
 */
const defaultProps = {
  total: 0
};

/**
 * Displays the component
 */
const ResultsTotal = props => {
  const { total } = props;
  const { t } = useTranslation("ResultsTotal");

  /**
   * Preparing the results array
   */
  const result = [
    t("results_total_prefix", { number: total }),
    t("results_total_companies")
  ].join(" ");

  return (
    <Grid item className="ResultsTotal">
      {result}
    </Grid>
  );
};

ResultsTotal.propTypes = propTypes;
ResultsTotal.defaultProps = defaultProps;

export default ResultsTotal;
export {
  propTypes as ResultsTotalPropTypes,
  defaultProps as ResultsTotalDefaultProps
};
