/**
 * Displays the language selector
 */
import React, { useState, useEffect } from "react";
import PropTypes from "prop-types";
import shortid from "shortid";

import { yup, useValidation } from "../../hooks";

import { makeStyles } from "@material-ui/core/styles";
import Grid from "@material-ui/core/Grid";
import FormControl from "@material-ui/core/FormControl";
import FormHelperText from "@material-ui/core/FormHelperText";
import TextField from "@material-ui/core/TextField";
import MenuItem from "@material-ui/core/MenuItem";

import i18n from "../../i18n";
import { useTranslation } from "react-i18next";
import { en_en } from "./LanguageSelector.lang.en-en";
import { nl_nl } from "./LanguageSelector.lang.nl-nl";
import { nl_be } from "./LanguageSelector.lang.nl-be";

/**
 * // TODO: There is no such language code like `en-EN` in https://en.wikipedia.org/wiki/Language_localisation as suggested in the requirement. Instead we have `en-GB` ... this has to be edited in the whole app, and in the generator

 > (like en-EN nl-NL, nl-BE, etc) is based on the &fd_lang=en-EN query or (if not

 */

i18n.addResourceBundle("en-EN", "LanguageSelector", en_en);
i18n.addResourceBundle("nl-NL", "LanguageSelector", nl_nl);
i18n.addResourceBundle("nl-BE", "LanguageSelector", nl_be);

/**
 * Defines the Language prop type
 */
const languagePropType = PropTypes.shape({
  name: PropTypes.string,
  code: PropTypes.string
});

/**
 * Defines the prop types
 */
const propTypes = {
  /**
   * See https://en.wikipedia.org/wiki/Language_localisation
   */
  languages: PropTypes.arrayOf(languagePropType),
  defaultLanguage: languagePropType
};

/**
 * Defines the default props
 */
const defaultProps = {
  languages: [
    { name: "English", code: "en-GB" },
    { name: "Dutch NL", code: "nl-NL" },
    { name: "Dutch BE", code: "nl-BE" }
  ],
  defaultLanguage: { name: "English", code: "en-GB" }
};

/**
 * Styles the component
 */
const useStyles = makeStyles(theme => ({
  formControl: {
    margin: theme.spacing(1),
    minWidth: 120,
    maxWidth: 300,
    width: "100%"
  }
}));

/**
 * Displays the component
 */
const LanguageSelector = props => {
  const { languages, defaultLanguage } = props;
  const { code } = defaultLanguage;

  const { t } = useTranslation("LanguageSelector");
  const classes = useStyles();

  /**
   * Manages the validation
   */
  const [isError, errorMessages, validateSchema] = useValidation({
    language: yup
      .string()
      .required()
      .typeError(t("It should be a string"))
  });

  /**
   * Manages the state of the select
   */
  const [selected, setSelected] = useState(code);

  const selectHandler = event => {
    setSelected(event.target.value);
  };

  useEffect(() => {
    validateSchema({ language: selected });
    i18n.changeLanguage(selected);
  }, [selected]);

  /**
   * Prepares the items
   */
  const items = languages.map(item => {
    const { name, code } = item;
    return (
      <MenuItem key={shortid.generate()} value={code}>
        {name}
      </MenuItem>
    );
  });

  return (
    <Grid item xs={12} className="LanguageSelector">
      <FormControl className={classes.formControl} error={isError}>
        <TextField
          name="language"
          select
          value={selected}
          onChange={selectHandler}
        >
          {items}
        </TextField>
        {isError && <FormHelperText>{errorMessages}</FormHelperText>}
      </FormControl>
    </Grid>
  );
};

LanguageSelector.propTypes = propTypes;
LanguageSelector.defaultProps = defaultProps;

export default LanguageSelector;
export {
  propTypes as LanguageSelectorPropTypes,
  defaultProps as LanguageSelectorDefaultProps
};
