/**
 * Displays the Country filter
 */
import React, { useState, useEffect, useContext } from "react";
import PropTypes from "prop-types";

import { useAPI, useAPIPropTypes, mergeApiParams } from "../../../hooks";

import { FilterContext } from "../Search";

import {
  displayCountries,
  CountriesPropTypes,
  CountriesDefaultProps
} from "../Countries";

import { makeStyles } from "@material-ui/core/styles";
import Grid from "@material-ui/core/Grid";
import InputLabel from "@material-ui/core/InputLabel";
import FormControl from "@material-ui/core/FormControl";
import Select from "@material-ui/core/Select";

import i18n from "../../../i18n";
import { useTranslation } from "react-i18next";
import { en_en } from "./FilterCountry.lang.en-en";
import { nl_nl } from "./FilterCountry.lang.nl-nl";
import { nl_be } from "./FilterCountry.lang.nl-be";

i18n.addResourceBundle("en-EN", "FilterCountry", en_en);
i18n.addResourceBundle("nl-NL", "FilterCountry", nl_nl);
i18n.addResourceBundle("nl-BE", "FilterCountry", nl_be);

/**
 * Defines the prop types
 *
 * @see http://api.finsterdata.com/v1/countries?token=d11e7916590604d1356a08579aad99dc6bd3cfd1
 */
const propTypes = {
  default_country_code: PropTypes.string,
  countries: PropTypes.shape(CountriesPropTypes),
  apiCall: PropTypes.shape(useAPIPropTypes)
};

/**
 * Defines the default props
 */
const defaultProps = {
  default_country_code: "GB",
  countries: CountriesDefaultProps,
  apiCall: {
    path: {
      endpoint: "countries"
    },
    params: {
      queryParams: {
        token: "d11e7916590604d1356a08579aad99dc6bd3cfd1"
      }
    },
    defaultData: CountriesDefaultProps.list
  }
};

/**
 * Styles the component
 */
const useStyles = makeStyles(theme => ({
  formControl: {
    margin: theme.spacing(1),
    minWidth: 120,
    maxWidth: 300,
    width: "100%"
  }
}));

/**
 * Displays the component
 */
const FilterCountry = props => {
  const { default_country_code, apiCall } = props;
  const { defaultData } = apiCall;

  const { t } = useTranslation("FilterCountry");
  const classes = useStyles();

  /**
   * Handles the filters
   */
  const { filters, setFilters } = useContext(FilterContext);
  const country = filters?.country || default_country_code;

  /**
   * Handles the API call
   */
  const [results, setResults] = useState(defaultData);

  const params = mergeApiParams({
    requestProps: apiCall
  });

  const { data } = useAPI(params);

  useEffect(() => {
    if (data && data.countries) {
      setResults(data.countries);
    }
  }, [data]);

  /**
   * Handles the select change
   */
  const selectHandler = data => {
    setFilters(prevState => {
      return { ...prevState, country: data.target.value };
    });
  };

  return (
    <Grid item xs={12} className="FilterCountry">
      <FormControl className={classes.formControl}>
        <InputLabel>{t("Country")}</InputLabel>
        <Select name="country" value={country} onChange={selectHandler}>
          {displayCountries({ list: results })}
        </Select>
      </FormControl>
    </Grid>
  );
};

FilterCountry.propTypes = propTypes;
FilterCountry.defaultProps = defaultProps;

export default FilterCountry;
export {
  propTypes as FilterCountryPropTypes,
  defaultProps as FilterCountryDefaultProps
};
