/**
 * Displays the Province filter
 */
import React, { useState, useEffect, useContext } from "react";
import PropTypes from "prop-types";
import shortid from "shortid";

import { useAPI, useAPIPropTypes, mergeApiParams } from "../../../hooks";

import { FilterContext } from "../Search";
import { displayFacets, FacetsPropTypes, FacetsDefaultProps } from "../Facets";

import { makeStyles } from "@material-ui/core/styles";
import Input from "@material-ui/core/Input";
import InputLabel from "@material-ui/core/InputLabel";
import FormControl from "@material-ui/core/FormControl";
import Select from "@material-ui/core/Select";
import Chip from "@material-ui/core/Chip";
import Grid from "@material-ui/core/Grid";

import i18n from "../../../i18n";
import { useTranslation } from "react-i18next";
import { en_en } from "./FilterProvince.lang.en-en";
import { nl_nl } from "./FilterProvince.lang.nl-nl";
import { nl_be } from "./FilterProvince.lang.nl-be";

i18n.addResourceBundle("en-EN", "FilterProvince", en_en);
i18n.addResourceBundle("nl-NL", "FilterProvince", nl_nl);
i18n.addResourceBundle("nl-BE", "FilterProvince", nl_be);

/**
 * Defines the prop types
 *
 * @see http://api.finsterdata.com/api/companies/?country=gb&facets=province&rows=0
 */
const propTypes = {
  provinces: PropTypes.shape(FacetsPropTypes),
  selected: PropTypes.arrayOf(PropTypes.string),
  apiCall: PropTypes.shape(useAPIPropTypes)
};

/**
 * Defines the default props
 */
const defaultProps = {
  provinces: FacetsDefaultProps,
  selected: [],
  apiCall: {
    path: {
      endpoint: "companies"
    },
    params: {
      queryParams: {
        facets: "province",
        rows: "0"
      }
    },
    defaultData: FacetsDefaultProps.list
  }
};

/**
 * Styles the component
 */
const useStyles = makeStyles(theme => ({
  formControl: {
    margin: theme.spacing(1),
    minWidth: 120,
    maxWidth: 300,
    width: "100%"
  },
  chips: {
    display: "flex",
    flexWrap: "wrap"
  },
  chip: {
    margin: 2,
    // Wrap long text
    height: "auto",
    padding: 4,
    "& .MuiChip-label": {
      overflowWrap: "break-word",
      whiteSpace: "normal",
      lineHeight: "1"
    }
  },
  noLabel: {
    marginTop: theme.spacing(3)
  }
}));

/**
 * Displays the component
 */
const FilterProvince = props => {
  const { selected: defaultSelected, apiCall } = props;
  const { defaultData } = apiCall;

  const { t } = useTranslation("FilterProvince");
  const classes = useStyles();

  /**
   * Handles the filters
   */
  const { filters, setFilters } = useContext(FilterContext);
  const selected = filters?.province || defaultSelected;

  /**
   * Handles the API call
   */
  const [results, setResults] = useState(defaultData);

  const token = "d11e7916590604d1356a08579aad99dc6bd3cfd1";
  const { country } = filters;

  const params = mergeApiParams({
    requestProps: apiCall,
    requestLiveProps: {
      params: { init: { body: { token: token, country: country } } }
    }
  });

  const { data } = useAPI(params);

  useEffect(() => {
    if (
      data &&
      data.facets &&
      data.facets.province &&
      data.facets.province.buckets
    ) {
      setResults(data.facets.province.buckets);
    }
  }, [data]);

  /**
   * Handles the select change
   */
  const selectHandler = data => {
    setFilters(prevState => {
      return { ...prevState, province: data.target.value };
    });
  };

  return (
    <Grid item xs={12} className="FilterProvince">
      <FormControl className={classes.formControl}>
        <InputLabel>{t("Province")}</InputLabel>
        <Select
          multiple
          name="province"
          value={selected}
          onChange={selectHandler}
          input={<Input id="select-multiple-chip" />}
          renderValue={selected => (
            <div className={classes.chips}>
              {selected.map(value => (
                <Chip
                  size="small"
                  key={shortid.generate()}
                  label={value}
                  className={classes.chip}
                />
              ))}
            </div>
          )}
        >
          {displayFacets({ list: results })}
        </Select>
      </FormControl>
    </Grid>
  );
};

FilterProvince.propTypes = propTypes;
FilterProvince.defaultProps = defaultProps;

export default FilterProvince;
export {
  propTypes as FilterProvincePropTypes,
  defaultProps as FilterProvinceDefaultProps
};
