import React from "react";

import ContactCard from "../ContactCard";
import ContactForm from "../ContactForm";

import Grid from "@material-ui/core/Grid";
import { makeStyles } from "@material-ui/core/styles";

import i18n from "../../../i18n";
//import { useTranslation } from "react-i18next";
import { en_en } from "./Contact.lang.en-en";
import { nl_nl } from "./Contact.lang.nl-nl";
import { nl_be } from "./Contact.lang.nl-be";

i18n.addResourceBundle("en-EN", "Contact", en_en);
i18n.addResourceBundle("nl-NL", "Contact", nl_nl);
i18n.addResourceBundle("nl-BE", "Contact", nl_be);

/**
 * Defines the prop types
 */
const propTypes = {};

/**
 * Defines the default props
 */
const defaultProps = {};

/**
 * Styles the component
 */
const useStyles = makeStyles(theme => ({
  contact: {
    ...theme.finster.grid.removeNegativeMargin,

    /**
     * Spacing, since we can't use spacing={4} because it adds also left and right spacing
     */
    [theme.breakpoints.up("sm")]: {
      marginBottom: theme.spacing(4),
      marginTop: theme.spacing(4)
    },

    [theme.breakpoints.up("xl")]: {
      justifyContent: "center"
    }
  }
}));

/**
 * Displays the component
 */
const Contact = props => {
  const classes = useStyles();
  const { contact } = classes;

  return (
    <Grid container className={contact} spacing={0}>
      <Grid item xs={12} md={6} xl={5}>
        <ContactCard />
      </Grid>
      <Grid item xs={12} md={6} xl={5}>
        <ContactForm />
      </Grid>
    </Grid>
  );
};

Contact.propTypes = propTypes;
Contact.defaultProps = defaultProps;

export default Contact;
export { propTypes as ContactPropTypes, defaultProps as ContactDefaultProps };
