/**
 * Displays a company in a popup
 *
 * - The popup can be triggered by a click on a company in the company list
 * - The popup can be accessed directly from the URL: http://localhost:3000/?fd=GB21259453
 *
 * - These two cases has to be handled inside a single component => that makes thi component's code a bit more complicated than usual
 */
import React, { useState, useEffect } from "react";
import PropTypes from "prop-types";

import {
  useAPI,
  useAPIPropTypes,
  mergeApiParams,
  useLocalStorage
} from "../../../hooks";

import {
  CompanyPropTypes,
  CompanyDefaultProps,
  companyGetLogo,
  companyGetRangeAndIcon,
  companyGetContactIcons,
  companyGetAddress,
  companyGetSocialIcons
} from "../Company";

import { makeStyles, useTheme } from "@material-ui/core/styles";
import Button from "@material-ui/core/Button";
import DialogActions from "@material-ui/core/DialogActions";
import DialogContent from "@material-ui/core/DialogContent";
import DialogContentText from "@material-ui/core/DialogContentText";
import DialogTitle from "@material-ui/core/DialogTitle";
import MobileStepper from "@material-ui/core/MobileStepper";
import KeyboardArrowLeft from "@material-ui/icons/KeyboardArrowLeft";
import KeyboardArrowRight from "@material-ui/icons/KeyboardArrowRight";

import i18n from "../../../i18n";
import { useTranslation } from "react-i18next";
import { en_en } from "./CompanyPopup.lang.en-en";
import { nl_nl } from "./CompanyPopup.lang.nl-nl";
import { nl_be } from "./CompanyPopup.lang.nl-be";

i18n.addResourceBundle("en-EN", "CompanyPopup", en_en);
i18n.addResourceBundle("nl-NL", "CompanyPopup", nl_nl);
i18n.addResourceBundle("nl-BE", "CompanyPopup", nl_be);

/**
 * Defines the prop types
 */
const propTypes = {
  /**
   * The company ID coming from the url params
   *
   * Ex.: NL12345678
   */
  fd: PropTypes.string,
  /**
   * The country filter value
   * - We need it for the `company` API call
   * - We can't get via `useContext` because the `Filters` context is not available when the company is directly accessed via direct routes (finsterdata.com?fd=NL12345678)
   */
  country: PropTypes.string,
  company: PropTypes.shape(CompanyPropTypes),
  apiCall: PropTypes.shape(useAPIPropTypes),
  /**
   * The list of companies in the current search.
   * - Used for prev / next navigation
   */
  navigationList: PropTypes.arrayOf(PropTypes.shape(CompanyPropTypes)),
  /**
   * The index of the current company displayed
   * - Used for prev / next navigation
   */
  navigationCurrentIndex: PropTypes.number,
  /**
   * The number of items displayed on a page
   * - Used for prev / next navigation
   */
  rows: PropTypes.number
};

/**
 * Defines the Default props
 */
const defaultProps = {
  fd: "",
  country: "",
  company: CompanyDefaultProps,
  apiCall: {
    path: {
      endpoint: "company"
    },
    defaultData: CompanyDefaultProps
  },
  navigationList: [CompanyDefaultProps],
  navigationCurrentIndex: 0,
  rows: 10
};

/**
 * Styles the component
 */
const useStyles = makeStyles(theme => ({
  root: {
    // Hide the prev/next navigation's 1/10 text ...
    color: "transparent"
  }
}));

/**
 * Displays the component
 */
const CompanyPopup = props => {
  const {
    fd,
    country: defaultCountry,
    company: defaultCompany,
    apiCall,
    navigationList,
    navigationCurrentIndex,
    rows,
    setOpened
  } = props;
  const { fdid: defaultFdid } = defaultCompany;

  const { t } = useTranslation("CompanyPopup");
  const theme = useTheme();
  const classes = useStyles();

  /**
   * Prepares the params for the API call
   *
   * - `country`, `fdid` can come an URL query param like http://api.finsterdata.com/v1/company?fd=GB21259453
   * - or simply from the caller component as props
   *
   * // TODO: Companies from Netherland have no `fd` or `fdid` at all
   */
  const token = "d11e7916590604d1356a08579aad99dc6bd3cfd1";
  const country = fd ? fd.split(/[^A-Z]/)[0] : defaultCountry;
  const currentFdid = fd ? fd.replace(/[^0-9]/g, "") : defaultFdid;

  /**
   * Handles the company fdid for the stepper
   */
  const [fdid, setFdid] = useState(currentFdid);

  /**
   * Handles the API call
   */
  const [result, setResult] = useState(defaultCompany);

  const params = mergeApiParams({
    requestProps: apiCall,
    requestLiveProps: {
      params: { init: { body: { token: token, fdid: fdid, country: country } } }
    }
  });

  const { data } = useAPI({ ...params, watch: fdid });

  useEffect(() => {
    if (data && data.company) {
      setResult(data.company);
    }
  }, [data]);

  /**
   * Handles the stepper
   */
  const [activeStep, setActiveStep] = React.useState(navigationCurrentIndex);
  const maxSteps = rows;

  const nextItem = navigationList[activeStep + 1];
  const nextFdid = nextItem ? nextItem.fdid : null;
  const prevItem = navigationList[activeStep - 1];
  const prevFdid = prevItem ? prevItem.fdid : null;

  const handleNext = () => {
    setActiveStep(prevActiveStep => prevActiveStep + 1);
    setFdid(nextFdid);
  };

  const handleBack = () => {
    setActiveStep(prevActiveStep => prevActiveStep - 1);
    setFdid(prevFdid);
  };

  /**
   * Loads visited companies list from the local storage
   *
   * - It's needed only when the company is accessed directly from the URL
   * - If accessed through a parent component (company list) another method will be used for marking visited
   */
  const [visited, setVisited] = useLocalStorage("visitedCompanies", "");

  /**
   * Marks visited companies
   */
  useEffect(() => {
    if (setOpened) {
      /**
       * The company was opened from the company list
       */
      setOpened(prevState => `${fdid},${prevState}`);
    } else {
      /**
       * The company was opened directly from the url
       */
      setVisited(prevState => `${fdid},${prevState}`);
    }
  }, [fdid]); // Here we can't include `setOpened`, `setVisited` since one of them is always undefined and will get the app into an exhaustive render cycle

  /**
   * Displays the company
   */
  const {
    name,
    city,
    entity,
    industry,
    employees_range,
    revenue_range
  } = result;

  return (
    <div className="CompanyPopup">
      <DialogTitle id="form-dialog-title">
        <img src={companyGetLogo(result)} alt={name} />, {name}, {city}
      </DialogTitle>
      <DialogContent>
        <DialogContentText>
          {t("Legal entity")}: {entity}, {t("Industry")}: {industry}
        </DialogContentText>
        <ul>
          <li>{t("Company info")}</li>
          <li>{companyGetRangeAndIcon(employees_range, "employees")}</li>
          <li>{companyGetRangeAndIcon(revenue_range, "revenue")}</li>
          <li>{companyGetContactIcons(result)}</li>
          <li>{companyGetAddress(result)}</li>
          <li>{t("Social media")}</li>
          <li>{companyGetSocialIcons(result)}</li>
        </ul>
      </DialogContent>
      <DialogActions>
        <Button variant="outlined" color="primary">
          {t("View Detail Page")}
        </Button>
      </DialogActions>
      {navigationList && setOpened && (
        <MobileStepper
          steps={maxSteps}
          position="static"
          variant="text"
          activeStep={activeStep}
          className={classes.root}
          nextButton={
            <Button
              size="small"
              onClick={handleNext}
              disabled={activeStep === maxSteps - 1}
            >
              {t("Next")}
              {theme.direction === "rtl" ? (
                <KeyboardArrowLeft />
              ) : (
                <KeyboardArrowRight />
              )}
            </Button>
          }
          backButton={
            <Button
              size="small"
              onClick={handleBack}
              disabled={activeStep === 0}
            >
              {theme.direction === "rtl" ? (
                <KeyboardArrowRight />
              ) : (
                <KeyboardArrowLeft />
              )}
              {t("Back")}
            </Button>
          }
        />
      )}
    </div>
  );
};

CompanyPopup.propTypes = propTypes;
CompanyPopup.defaultProps = defaultProps;

export default CompanyPopup;
export {
  propTypes as CompanyPopupPropTypes,
  defaultProps as CompanyPopupDefaultProps
};
