/**
 * - Displays a company
 * - Defines functions to display various parts of the company
 */
import React from "react";
import PropTypes from "prop-types";

import Tooltip from "@material-ui/core/Tooltip";

import i18n from "../../../i18n";
import { useTranslation } from "react-i18next";
import { en_en } from "./Company.lang.en-en";
import { nl_nl } from "./Company.lang.nl-nl";
import { nl_be } from "./Company.lang.nl-be";

i18n.addResourceBundle("en-EN", "Company", en_en);
i18n.addResourceBundle("nl-NL", "Company", nl_nl);
i18n.addResourceBundle("nl-BE", "Company", nl_be);

/**
 * Defines the prop types
 *
 * @see http://api.finsterdata.com/v1/company?fd=GB21259453&token=d11e7916590604d1356a08579aad99dc6bd3cfd1
 *
 * // TODO: Why Pinterest link is an array, the rest is a string
 */
const propTypes = {
  id: PropTypes.string,
  fd: PropTypes.string,
  fdid: PropTypes.number,
  name: PropTypes.string,
  city: PropTypes.string,
  postcode: PropTypes.string,
  country_code: PropTypes.string,
  employees: PropTypes.number,
  email: PropTypes.string,
  nave: PropTypes.arrayOf(PropTypes.string),
  domain: PropTypes.string,
  url: PropTypes.string,
  phone: PropTypes.string,
  phone_international: PropTypes.string,
  province: PropTypes.string,
  region: PropTypes.string,
  entity: PropTypes.string,
  industry: PropTypes.arrayOf(PropTypes.string),
  employees_range: PropTypes.number,
  revenue_range: PropTypes.number,
  facebook_link: PropTypes.string,
  twitter_link: PropTypes.string,
  linkedin_link: PropTypes.string,
  youtube_link: PropTypes.string,
  instagram_link: PropTypes.string,
  pinterest_link: PropTypes.arrayOf(PropTypes.string),
  downloaded: PropTypes.string,
  viewed: PropTypes.string,
  enriched: PropTypes.string,
  marked: PropTypes.string,
  first_seen: PropTypes.string,
  last_seen: PropTypes.string,
  /**
   * Props coming from the `action=list` for CompanySmallProfile
   */
  data_exists: PropTypes.arrayOf(PropTypes.string)
};

/**
 * Defines the default props
 */
const defaultProps = {
  id: "id",
  name: "Company name",
  city: "City",
  address: "Address",
  postcode: "Postcode",
  country_code: "Country code",
  employees: null,
  email: "email",
  nace: null,
  domain: null,
  url: null,
  phone: null,
  phone_international: null,
  fdid: null,
  province: null,
  region: null,
  entity: null,
  industry: null,
  employees_range: null,
  revenue_range: null,
  facebook_link: null,
  twitter_link: null,
  linkedin_link: null,
  fd: null,
  youtube_link: null,
  instagram_link: null,
  pinterest_link: null,
  downloaded: null,
  viewed: null,
  enriched: null,
  marked: null,
  first_seen: null,
  last_seen: null,
  data_exists: null
};

/**
 * Props not coming from the API
 */
const otherPropTypes = {
  logourl: PropTypes.string,
  defaultDomain: PropTypes.string
};

const otherProps = {
  logourl: "http://logo.finsterdata.com/img/",
  defaultDomain: null
};

/**
 * Converts `revenue_range` from a number to a string
 */
const convertRevenueRange = range => {
  switch (range) {
    case 100:
      return "<0,1M";
    case 500:
      return "0,1-0,5M";
    case 1000:
      return "0,5-1M";
    case 2000:
      return "1-2M";
    case 5000:
      return "2-5M";
    case 10000:
      return "5-10M";
    case 25000:
      return "10-25M";
    case 50000:
      return "25-50M";
    case 100000:
      return "50-100M";
    case 250000:
      return "100-250M";
    case 500000:
      return "250-500M";
    case 10000000:
      return "500M+";
    case -1:
    default:
      return "unknown";
  }
};

/**
 * Converts `employees_range` from a number to a string
 */
const convertEmployeeRange = range => {
  switch (range) {
    case 1:
      return "1";
    case 5:
      return "2-5";
    case 10:
      return "6-10";
    case 20:
      return "11-20";
    case 50:
      return "21-50";
    case 100:
      return "51-100";
    case 250:
      return "101-250";
    case 500:
      return "251-500";
    case 1000:
      return "501-1000";
    case 2500:
      return "1001-2500";
    case 5000:
      return "2501-5000";
    case 100000:
      return "5001-1000";
    case 10000000:
      return "10.001+";
    case -1:
    default:
      return "unknown";
  }
};

/**
 * Returns the logo
 */
const getLogo = props => {
  const { domain } = props;
  const { logourl, defaultDomain } = otherProps;

  const d = domain ? domain : defaultDomain;

  return `${logourl}${d}`;
};

/**
 * Returns an icon and a range
 */
const getRangeAndIcon = (range, type) => {
  const value =
    type === "employees"
      ? convertEmployeeRange(range)
      : convertRevenueRange(range);

  return value === "unknown" ? "" : `${value}`;
};

/**
 * Returns the contact icons
 *
 * - The icons are returned with the links / additional data
 * - See `CompanyPopup.md` for details
 */
const getContactIcons = props => {
  const { email, phone_international, url, tooltips } = props;

  /**
   * In the list view tooltips have to be displayed. Otherwise we need no tooltips
   */
  const tooltips2 = tooltips ? tooltips : ["", "", ""];

  return (
    <ul className="ContactIcons">
      <li>
        <Tooltip title={tooltips2[0]}>
          <div>PhoneIcon: {phone_international}</div>
        </Tooltip>
      </li>
      <li>
        <Tooltip title={tooltips2[1]}>
          <div>UrlIcon: {url}</div>
        </Tooltip>
      </li>
      <li>
        <Tooltip title={tooltips2[2]}>
          <div>EmailIcon: {email}</div>
        </Tooltip>
      </li>
    </ul>
  );
};

/**
 * Returns the contact icons from `data_exists`
 *
 * - Only the icons are returned without the links / additional data
 *
 * // NOTE: Without `action=list` the `data_exists` field is not returned by the API
 */
const getContactIconsFromDataExists = props => {
  const { data_exists, available } = props;

  return data_exists
    ? data_exists.map(item => (
        <Tooltip title={`${item} ${available}`}>
          <div>{item}</div>
        </Tooltip>
      ))
    : "";
};

/**
 * Returns the address
 *
 * - See `CompanyPopup.md` for details
 */
const getAddress = props => {
  const { name, address, postcode, city, country_code } = props;

  return `AddressIcon: ${name}, ${address}, ${postcode} ${city}, ${country_code}`;
};

/**
 * Returns the social icons and links
 *
 * - See `CompanyPopup.md` for details
 */
const getSocialIcons = props => {
  const {
    facebook_link,
    twitter_link,
    linkedin_link,
    youtube_link,
    instagram_link,
    pinterest_link
  } = props;

  let links = facebook_link ? `FacebookIcon: ${facebook_link}` : "";
  links += twitter_link ? `TwitterIcon: ${twitter_link}` : "";
  links += linkedin_link ? `LinkedinIcon: ${linkedin_link}` : "";
  links += youtube_link ? `YoutubeIcon: ${youtube_link}` : "";
  links += instagram_link ? `InstagramIcon: ${instagram_link}` : "";
  links +=
    pinterest_link && pinterest_link[0]
      ? `PinterestIcon: ${pinterest_link[0]}`
      : "";

  return links;
};

/**
 * Displays the component
 */
const Company = props => {
  const {
    name,
    city,
    entity,
    industry,
    employees_range,
    revenue_range
  } = props;

  const { t } = useTranslation(["Company", "CompanyPopup"]);
  const tooltips = [
    t("Phone available"),
    t("Website available"),
    t("Email available")
  ];

  return (
    <div className="Company">
      {t("Company")}
      <ul>
        <li>
          <b>Header</b>
        </li>
        <li>
          <img alt={name} src={getLogo(props)} />
        </li>
        <li>{name}</li>
        <li>{city}</li>
        <li>
          {t("CompanyPopup:Legal entity")}: {entity}
        </li>
        <li>
          {t("CompanyPopup:Industry")}: {industry}
        </li>
        <li>{getSocialIcons(props)}</li>
        <li>
          <b>Details</b>
        </li>
        <li>{getRangeAndIcon(employees_range, "employees")}</li>
        <li>{getRangeAndIcon(revenue_range, "revenue")}</li>
        <li>
          {getContactIcons({
            ...props,
            tooltips: tooltips
          })}
        </li>
        <li>{getAddress(props)}</li>
        <li>
          <b>Tabs and Tab content (in a next version)</b>
        </li>
      </ul>
    </div>
  );
};

Company.propTypes = propTypes;
Company.defaultProps = defaultProps;

export default Company;
export {
  propTypes as CompanyPropTypes,
  defaultProps as CompanyDefaultProps,
  getLogo as companyGetLogo,
  getRangeAndIcon as companyGetRangeAndIcon,
  getContactIcons as companyGetContactIcons,
  getContactIconsFromDataExists as companyGetContactIconsFromDataExists,
  getAddress as companyGetAddress,
  getSocialIcons as companyGetSocialIcons
};
