import React, { useContext, createContext } from 'react';
import useUpgradesData from './useUpgradesData';

const UpgradesContext = createContext();

const UpgradesProvider = ({ children }) => {
  const upgrades = useUpgradesData();

  return (
    <UpgradesContext.Provider value={upgrades}>
      {children}
    </UpgradesContext.Provider>
  );
};

const useUpgrades = () => {
  return useContext(UpgradesContext);
};

export default useUpgrades;
export { useUpgrades, UpgradesProvider };
