import React, { useState, useEffect, createContext } from "react";
import PropTypes from "prop-types";

import {
  useAuth,
  useAPI,
  useAPIPropTypes,
  mergeApiParams,
  isApiError,
  getApiErrorMessage
} from "../../../hooks";

import ResultsTotal from "../ResultsTotal";
import Filters from "../Filters";
import FilteredBy from "../FilteredBy";
import Results, { ResultsPropTypes, ResultsDefaultProps } from "../Results";
import { FacetsPropTypes, FacetsDefaultProps } from "../Facets";

import Grid from "@material-ui/core/Grid";
import Snackbar from "@material-ui/core/Snackbar";
import IconButton from "@material-ui/core/IconButton";
import CloseIcon from "@material-ui/icons/Close";

import i18n from "../../../i18n";
import { useTranslation } from "react-i18next";
import { en_en } from "./Search.lang.en-en";
import { nl_nl } from "./Search.lang.nl-nl";
import { nl_be } from "./Search.lang.nl-be";

i18n.addResourceBundle("en-EN", "Search", en_en);
i18n.addResourceBundle("nl-NL", "Search", nl_nl);
i18n.addResourceBundle("nl-BE", "Search", nl_be);

/**
 * Defines the prop types
 *
 * @see Search.md for details
 *
 * // TODO: Filters are a total mess:
 * - some of them are not present on the UI (region), some present on the UI are not present in the requirements (industry)
 * - don't know if `location` or `city` to use ...
 */
const propTypes = {
  /**
   * Params for the search query, which aren't filter related
   */
  inputs: PropTypes.shape({
    token: PropTypes.string,
    start: PropTypes.string,
    //page: PropTypes.string, moved to filters...
    //rows: PropTypes.string,
    format: PropTypes.oneOf(["json", "xml", "csv"]),
    // See ResultsDownload.md for this param
    action: PropTypes.oneOf(["list", "download"]),
    facets: PropTypes.bool,
    simple: PropTypes.bool,
    debug: PropTypes.bool
  }),
  /**
   * Params for the search query, filter related
   */
  filters: PropTypes.shape({
    country: PropTypes.string,
    id: PropTypes.string,
    revenue: PropTypes.string,
    employees: PropTypes.string,
    required_data: PropTypes.arrayOf(PropTypes.string),
    location: PropTypes.string,
    geo: PropTypes.string,
    km: PropTypes.string,
    text: PropTypes.string,
    province: PropTypes.arrayOf(PropTypes.string),
    region: PropTypes.arrayOf(PropTypes.string),
    category: PropTypes.arrayOf(PropTypes.string),
    apps: PropTypes.arrayOf(PropTypes.string),
    apps_cat: PropTypes.arrayOf(PropTypes.string),
    nace: PropTypes.arrayOf(PropTypes.string),
    naic: PropTypes.arrayOf(PropTypes.string),
    entity: PropTypes.arrayOf(PropTypes.string),
    age: PropTypes.number,
    // From inputs ....
    page: PropTypes.number,
    rows: PropTypes.number
  }),
  /**
   * ?????
   */
  fields: PropTypes.string,
  /**
   * Params for the API call
   */
  apiCall: PropTypes.shape(useAPIPropTypes),
  /**
   * Params for the search results, which aren't filter related
   */
  results: PropTypes.shape(ResultsPropTypes),
  /**
   * Params for the search query, which are filter related
   */
  facets: PropTypes.shape({
    data_exists: PropTypes.shape(FacetsPropTypes),
    entity: PropTypes.shape(FacetsPropTypes),
    revenue_range: PropTypes.shape(FacetsPropTypes),
    province: PropTypes.shape(FacetsPropTypes),
    region: PropTypes.shape(FacetsPropTypes),
    industry: PropTypes.shape(FacetsPropTypes),
    industry_naic: PropTypes.shape(FacetsPropTypes)
  })
};

/**
 * Defines the default props
 */
const defaultProps = {
  inputs: {
    token: "",
    start: "0",
    format: "json",
    action: "list",
    facets: false,
    simple: false,
    debug: false
  },
  filters: {
    country: "GB",
    id: null, // for https://github.com/sindresorhus/query-string#skipnull
    revenue: null,
    employees: null,
    required_data: null,
    location: null,
    geo: null,
    km: null,
    text: null,
    province: null,
    region: null,
    category: null,
    nace: null,
    naic: null,
    entity: null,
    age: 0,
    page: 1,
    rows: 10
  },
  fields:
    "name,phone,url,address,city,postcode,province,region,orgtype,employees,revenue,founded_date,industry,nace,naic",
  apiCall: {
    path: {
      endpoint: "companies"
    },
    params: {
      queryParams: {
        //action: "list", This doesn't returns the industry facet !!! See FilterIndustry for details
        facets: true
      }
    },
    defaultData: ResultsDefaultProps
  },
  results: ResultsDefaultProps,
  facets: {
    data_exists: FacetsDefaultProps,
    entity: FacetsDefaultProps,
    revenue_range: FacetsDefaultProps,
    province: FacetsDefaultProps,
    region: FacetsDefaultProps,
    industry: FacetsDefaultProps,
    industry_naic: FacetsDefaultProps
  }
};

/**
 * Creates a filter context
 */
const FilterContext = createContext();

/**
 * Displays the component
 */
const Search = props => {
  const {
    results: defaultResults,
    filters: defaultFilters,
    facets: defaultFacets,
    apiCall
  } = props;

  const { t } = useTranslation("LanguageSelector");
  const { isAuthenticated, token, redirectTo } = useAuth();

  /**
   * If the user is unauthenticated redirect to the homepage
   */
  if (!isAuthenticated) redirectTo("/login");

  /**
   * Handles the filters
   */
  const [filters, setFilters] = useState(defaultFilters);

  /**
   * Handles the results
   */
  const [results, setResults] = useState(defaultResults);
  const [total, setTotal] = useState(0);
  const [message, setMessage] = useState("");
  const [facets, setFacets] = useState(defaultFacets);

  /**
   * Handles the notification bar
   */
  const [snackbarOpen, setSnackbarOpen] = useState(false);

  const handleSnackbarClose = (event, reason) => {
    setSnackbarOpen(false);
  };

  /**
   * Handles the API call
   */
  const params = mergeApiParams({
    requestProps: apiCall,
    requestLiveProps: {
      params: { init: { body: { ...filters, token: token } } }
    }
  });

  const { data } = useAPI({ ...params, watch: filters });

  useEffect(() => {
    /**
     * Handles the errors
     */
    if (isApiError(data)) {
      setResults(defaultResults);
      setTotal(0);
      setMessage(t(getApiErrorMessage(data)));
      setSnackbarOpen(true);
    } else {
      setMessage("");
    }

    /**
     * Handles the companies
     *
     * - data.companies (array) has to be mapped to results.companies.list (array)
     * - if `data.companies.list` exists it means currently the default values are processed. In this case we don't do the mapping
     */
    if (data && data.companies && !data.companies.list) {
      setResults(prevState => {
        return { ...prevState, companies: { list: data.companies } };
      });
    }

    /**
     * Handles the facets returned when a country is selected
     */
    if (data && data.facets) {
      setFacets(data.facets);
    }

    /**
     * Handles the total companies found
     */
    if (data && data.found) {
      setTotal(data.found);
    }

    /**
     * Handles the downlaod data section
     */
    if (data && data.found && data.credits) {
      setResults(prevState => {
        return {
          ...prevState,
          download: { buttons: { found: data.found, credits: data.credits } }
        };
      });
    }
  }, [data, defaultResults]);

  return (
    <>
      <FilterContext.Provider
        value={{ filters: filters, setFilters: setFilters, facets: facets }}
      >
        <Grid container spacing={4} className="Search">
          <Grid item xs={12} md={3}>
            <Grid container spacing={4}>
              <ResultsTotal total={total} />
              <FilteredBy />
              <Filters />
            </Grid>
          </Grid>
          <Grid item xs={12} md={9}>
            <Results {...results} total={total} />
          </Grid>
        </Grid>
      </FilterContext.Provider>

      <Snackbar
        anchorOrigin={{
          vertical: "bottom",
          horizontal: "center"
        }}
        open={snackbarOpen}
        autoHideDuration={3000}
        onClose={handleSnackbarClose}
        message={message}
        action={
          <React.Fragment>
            <IconButton
              size="small"
              aria-label="close"
              color="inherit"
              onClick={handleSnackbarClose}
            >
              <CloseIcon fontSize="small" />
            </IconButton>
          </React.Fragment>
        }
      />
    </>
  );
};

Search.propTypes = propTypes;
Search.defaultProps = defaultProps;

export default Search;
export {
  propTypes as SearchPropTypes,
  defaultProps as SearchDefaultProps,
  FilterContext
};
