/**
 * Displays a list of companies
 */
import React, { useContext } from "react";
import PropTypes from "prop-types";
import shortid from "shortid";

import { useLocalStorage } from "../../../hooks";

import { FilterContext } from "../Search";

import CompanyList, {
  CompanyListPropTypes,
  CompanyListDefaultProps
} from "../CompanyList";
import PaginationRowsPerPage, {
  PaginationRowsPerPagePropTypes,
  PaginationRowsPerPageDefaultProps
} from "../PaginationRowsPerPage";

import { makeStyles } from "@material-ui/core/styles";
import ListItem from "@material-ui/core/ListItem";
import List from "@material-ui/core/List";
import Pagination from "@material-ui/lab/Pagination";

import i18n from "../../../i18n";
//import { useTranslation } from "react-i18next";
import { en_en } from "./Companies.lang.en-en";
import { nl_nl } from "./Companies.lang.nl-nl";
import { nl_be } from "./Companies.lang.nl-be";

i18n.addResourceBundle("en-EN", "Companies", en_en);
i18n.addResourceBundle("nl-NL", "Companies", nl_nl);
i18n.addResourceBundle("nl-BE", "Companies", nl_be);

/**
 * Defines the prop types
 */
const propTypes = {
  list: PropTypes.arrayOf(PropTypes.shape(CompanyListPropTypes)),
  total: PropTypes.number,
  rowsPerPage: PropTypes.shape(PaginationRowsPerPagePropTypes)
};

/**
 * Defines the default props
 */
const defaultProps = {
  list: [CompanyListDefaultProps],
  total: 0,
  rowsPerPage: PaginationRowsPerPageDefaultProps
};

/**
 * Styles the component
 */
const useStyles = makeStyles(theme => ({
  root: {
    width: "100%",
    cursor: "pointer"
  }
}));

/**
 * Displays the component
 */
const Companies = props => {
  const { list, total, rowsPerPage } = props;

  const classes = useStyles();

  /**
   * Handles the pagination
   */
  const { filters, setFilters } = useContext(FilterContext);
  const { country, page, rows } = filters;
  const totalPages = Math.round(total / rows);

  const handlePagination = (event, value) => {
    setFilters(prevState => {
      return { ...prevState, page: value };
    });
  };

  /**
   * Marks visited companies
   *
   * // TODO: this is replicated in CompanyPopup ... it should be extracted into a hook
   */
  const [visited, setVisited] = useLocalStorage("visitedCompanies", "");

  /**
   * Prepares the list items
   */
  const items = list.map((item, index) => {
    const { fdid, viewed } = item;
    const isVisited = visited.includes(fdid) || viewed > 0;

    const companyPopup = {
      company: item,
      country: country,
      navigationList: list,
      navigationCurrentIndex: index
    };

    return (
      <ListItem key={shortid.generate()} className={`${classes.root} ListItem`}>
        <CompanyList
          companyPopup={companyPopup}
          isVisited={isVisited}
          setVisited={setVisited}
        />
      </ListItem>
    );
  });

  return (
    <div className="Companies">
      <List className="List">{items}</List>
      {totalPages > 0 && (
        <>
          <Pagination
            count={totalPages}
            page={page}
            onChange={handlePagination}
          />
          <PaginationRowsPerPage {...rowsPerPage} />
        </>
      )}
    </div>
  );
};

Companies.propTypes = propTypes;
Companies.defaultProps = defaultProps;

export default Companies;
export {
  propTypes as CompaniesPropTypes,
  defaultProps as CompaniesDefaultProps
};
