import React, { useContext } from "react";
import PropTypes from "prop-types";

import {
  useAPIPropTypes,
  mergeApiParams,
  createPathToResource
} from "../../../hooks";

import { FilterContext } from "../Search";

import { convertRecordToRow } from "../ResultsDownloadRecords";

import Button from "@material-ui/core/Button";

import i18n from "../../../i18n";
import { useTranslation } from "react-i18next";
import { en_en } from "./ResultsDownloadButtons.lang.en-en";
import { nl_nl } from "./ResultsDownloadButtons.lang.nl-nl";
import { nl_be } from "./ResultsDownloadButtons.lang.nl-be";

i18n.addResourceBundle("en-EN", "ResultsDownloadButtons", en_en);
i18n.addResourceBundle("nl-NL", "ResultsDownloadButtons", nl_nl);
i18n.addResourceBundle("nl-BE", "ResultsDownloadButtons", nl_be);

/**
 * Defines the prop types
 */
const propTypes = {
  found: PropTypes.number,
  credits: PropTypes.number,
  plan_id: PropTypes.number,
  apiCall: PropTypes.shape(useAPIPropTypes)
};

/**
 * Defines the default props
 */
const defaultProps = {
  found: 0,
  credits: 0,
  plan_id: 0,
  apiCall: {
    path: {
      endpoint: "companies"
    },
    params: {
      queryParams: {
        token: "d11e7916590604d1356a08579aad99dc6bd3cfd1",
        data_exists: "url",
        action: "download",
        facets: true
      }
    },
    defaultData: ""
  }
};

/**
 * Displays the component
 */
const ResultsDownloadButtons = props => {
  const {
    found,
    credits,
    plan_id,
    apiCall,
    fileType,
    record,
    otherAmount
  } = props;

  const { t } = useTranslation("ResultsDownloadButtons");
  const upgrade = t("Show upgrade options to download all records");
  const download = t("Download");

  /**
   * Creates a link to download
   */
  const { filters } = useContext(FilterContext);

  const params = mergeApiParams({
    requestProps: apiCall,
    requestLiveProps: {
      params: {
        init: {
          body: {
            ...filters,
            output: fileType,
            rows: convertRecordToRow({
              record: record,
              otherAmount: otherAmount
            })
          }
        }
      }
    }
  });

  const linkToDownload = createPathToResource(params);

  /**
   * Creates a text for the button
   */
  const text = found > credits ? upgrade : download;
  const link =
    plan_id > 0 && plan_id < 5 ? "link to subscriptions" : linkToDownload;

  return (
    <Button
      variant="outlined"
      color="primary"
      className="ResultsDownloadButtons"
      href={link}
    >
      {text} ({link})
    </Button>
  );
};

ResultsDownloadButtons.propTypes = propTypes;
ResultsDownloadButtons.defaultProps = defaultProps;

export default ResultsDownloadButtons;
export {
  propTypes as ResultsDownloadButtonsPropTypes,
  defaultProps as ResultsDownloadButtonsDefaultProps
};
