import React from "react";
import PropTypes from "prop-types";

/**
 * See https://material-ui.com/components/icons/#svgicon
 */
import SvgIcon from "@material-ui/core/SvgIcon";
/**
 * The icon has to be preprocessed by svgr:
 *
 * ```
 * npx @svgr/cli src/components/home/FeatureA/printer.svg > src/components/home/FeatureA/SVGIconPrinter.js --icon --replace-attr-values "#fa9140=currentColor"
```
 */
import SVGIconEmpty from "./SVGIconEmpty";

import { makeStyles } from "@material-ui/core/styles";

import i18n from "../../i18n";
//import { useTranslation } from "react-i18next";
import { en_en } from "./Icon.lang.en-en";
import { nl_nl } from "./Icon.lang.nl-nl";
import { nl_be } from "./Icon.lang.nl-be";

i18n.addResourceBundle("en-EN", "Icon", en_en);
i18n.addResourceBundle("nl-NL", "Icon", nl_nl);
i18n.addResourceBundle("nl-BE", "Icon", nl_be);

/**
 * Defines the prop types
 *
 * @see also https://material-ui.com/api/svg-icon/
 */
const propTypes = {
  svg: PropTypes.any,
  viewBox: PropTypes.string,
  size: PropTypes.oneOf(["", "default", "inherit", "large", "small"])
};

/**
 * Defines the default props
 */
const defaultProps = {
  svg: SVGIconEmpty,
  viewBox: "0 0 24 24",
  size: ""
};

/**
 * Styles the component
 */
const useStyles = makeStyles(theme => ({
  icon: {
    borderStyle: "solid",
    borderWidth: 1,
    borderRadius: "50%",
    borderColor: theme.palette.primary.main,
    padding: theme.spacing(1),

    display: "flex",
    alignItems: "center",
    justifyContent: "center",

    ["&.icon-size-large"]: {
      borderWidth: 2
    }
  }
}));

/**
 * Displays the component
 */
const Icon = props => {
  const { svg, viewBox, size } = props;

  const classes = useStyles();
  const { icon } = classes;

  return (
    <div className={`${icon} icon-size-${size}`}>
      <SvgIcon fontSize={size} component={svg} viewBox={viewBox} />
    </div>
  );
};

Icon.propTypes = propTypes;
Icon.defaultProps = defaultProps;

export default Icon;
export { propTypes as IconPropTypes, defaultProps as IconDefaultProps };
