/**
 * Displays the Employee filter
 */
import React, { useContext, useRef } from "react";
import PropTypes from "prop-types";

import { FilterContext } from "../Search";

import { makeStyles } from "@material-ui/core/styles";
import Input from "@material-ui/core/Input";
import FormControl from "@material-ui/core/FormControl";
import InputLabel from "@material-ui/core/InputLabel";
import Grid from "@material-ui/core/Grid";

import i18n from "../../../i18n";
import { useTranslation } from "react-i18next";
import { en_en } from "./FilterEmployees.lang.en-en";
import { nl_nl } from "./FilterEmployees.lang.nl-nl";
import { nl_be } from "./FilterEmployees.lang.nl-be";

i18n.addResourceBundle("en-EN", "FilterEmployees", en_en);
i18n.addResourceBundle("nl-NL", "FilterEmployees", nl_nl);
i18n.addResourceBundle("nl-BE", "FilterEmployees", nl_be);

/**
 * Defines the prop types
 */
const propTypes = {
  defaultValueFrom: PropTypes.number,
  defaultValueTo: PropTypes.number
};

/**
 * Defines the default props
 */
const defaultProps = {
  defaultValueFrom: 2,
  defaultValueTo: 10
};

/**
 * Styles the component
 */
const useStyles = makeStyles(theme => ({
  formControl: {
    margin: theme.spacing(1),
    minWidth: 120,
    maxWidth: 300,
    width: "100%",
    display: "flex",
    flexDirection: "row",
    alignItems: "flex-end"
  },
  input1: {
    marginRight: 8
  }
}));

/**
 * Displays the component
 */
const FilterEmployees = props => {
  const { defaultValueFrom, defaultValueTo } = props;

  const { filters, setFilters } = useContext(FilterContext);
  const from = filters?.from || defaultValueFrom;
  const to = filters?.to || defaultValueTo;

  const { t } = useTranslation("FilterEmployees");
  const classes = useStyles();

  /**
   * Manages the input values
   */
  const inputRefFrom = useRef(null);
  const inputRefTo = useRef(null);

  /**
   * Handles the input change
   */
  const inputHandlerFrom = data => {
    const value = inputRefFrom.current.value;

    setFilters(prevState => {
      return { ...prevState, from: value };
    });
  };

  const inputHandlerTo = data => {
    const value = inputRefTo.current.value;

    setFilters(prevState => {
      return { ...prevState, to: value };
    });
  };

  return (
    <Grid item xs={12} className="FilterEmployees">
      <FormControl className={classes.formControl}>
        <InputLabel>{t("Employees")}</InputLabel>
        <Input
          name="from"
          type="number"
          className={classes.input1}
          value={from}
          onChange={inputHandlerFrom}
          inputRef={inputRefFrom}
        />
        <Input
          name="to"
          type="number"
          value={to}
          onChange={inputHandlerTo}
          inputRef={inputRefTo}
        />
      </FormControl>
    </Grid>
  );
};

FilterEmployees.propTypes = propTypes;
FilterEmployees.defaultProps = defaultProps;

export default FilterEmployees;
export {
  propTypes as FilterEmployeesPropTypes,
  defaultProps as FilterEmployeesDefaultProps
};
