/**
 * Displays the age filter
 */
import React, { useContext } from "react";
import PropTypes from "prop-types";
import shortid from "shortid";

import { FilterContext } from "../Search";

import { makeStyles } from "@material-ui/core/styles";
import InputLabel from "@material-ui/core/InputLabel";
import MenuItem from "@material-ui/core/MenuItem";
import FormControl from "@material-ui/core/FormControl";
import Select from "@material-ui/core/Select";
import Grid from "@material-ui/core/Grid";

import i18n from "../../../i18n";
import { useTranslation } from "react-i18next";
import { en_en } from "./FilterAge.lang.en-en";
import { nl_nl } from "./FilterAge.lang.nl-nl";
import { nl_be } from "./FilterAge.lang.nl-be";

i18n.addResourceBundle("en-EN", "FilterAge", en_en);
i18n.addResourceBundle("nl-NL", "FilterAge", nl_nl);
i18n.addResourceBundle("nl-BE", "FilterAge", nl_be);

/**
 * Defines the prop types
 */
const propTypes = {
  values: PropTypes.arrayOf(
    PropTypes.shape({
      name: PropTypes.string,
      value: PropTypes.number
    })
  )
};

/**
 * Defines the default props
 */
const defaultProps = {
  values: [
    { name: "1 month", value: 31 },
    { name: "3 months", value: 93 },
    { name: "6 months", value: 186 },
    { name: "1 year", value: 365 },
    { name: "2 years", value: 730 },
    { name: "3 years", value: 1095 },
    { name: "4 years", value: 1460 },
    { name: "5 years", value: 1825 },
    { name: "10 years", value: 3650 }
  ]
};

/**
 * Styles the component
 */
const useStyles = makeStyles(theme => ({
  formControl: {
    margin: theme.spacing(1),
    minWidth: 120,
    maxWidth: 300,
    width: "100%"
  }
}));

/**
 * Displays the component
 */
const FilterAge = props => {
  const { values } = props;

  const { filters, setFilters } = useContext(FilterContext);
  const age = filters?.age || values[0].value;

  const { t } = useTranslation("FilterAge");
  const classes = useStyles();

  /**
   * Handles the select change
   */
  const selectHandler = data => {
    setFilters(prevState => {
      return { ...prevState, age: data.target.value };
    });
  };

  /**
   * Prepares the items
   */
  const items = values.map(item => {
    const { name, value } = item;

    return (
      <MenuItem key={shortid.generate()} value={value}>
        {t(name)}
      </MenuItem>
    );
  });

  return (
    <Grid item xs={12} className="FilterAge">
      <FormControl className={classes.formControl}>
        <InputLabel>{t("Age")}</InputLabel>
        <Select name="age" value={age} onChange={selectHandler}>
          {items}
        </Select>
      </FormControl>
    </Grid>
  );
};

FilterAge.propTypes = propTypes;
FilterAge.defaultProps = defaultProps;

export default FilterAge;
export {
  propTypes as FilterAgePropTypes,
  defaultProps as FilterAgeDefaultProps
};
