import React from "react";

import Logo from "../Logo";
import SiteTitle from "../SiteTitle";
import Menu from "../Menu";

import Grid from "@material-ui/core/Grid";
import AppBar from "@material-ui/core/AppBar";
import Toolbar from "@material-ui/core/Toolbar";

import i18n from "../../i18n";
//import { useTranslation } from "react-i18next";
import { en_en } from "./Header.lang.en-en";
import { nl_nl } from "./Header.lang.nl-nl";
import { nl_be } from "./Header.lang.nl-be";

i18n.addResourceBundle("en-EN", "Header", en_en);
i18n.addResourceBundle("nl-NL", "Header", nl_nl);
i18n.addResourceBundle("nl-BE", "Header", nl_be);

/**
 * Defines the prop types
 */
const propTypes = {};

/**
 * Defines the default props
 */
const defaultProps = {};

/**
 * Displays the component
 *
 * The fixed header was removed. It's now static.
 * - To implement a fixed header with variable background color (as is in the design) an intersection observer has to be employed watching the scroll. That would be too much performance degradataion for such a minor gain.
 */
const Header = props => {
  const { currentPage } = props;

  return (
    <AppBar
      className="Header"
      color="transparent"
      elevation={0}
      position="static"
    >
      <Toolbar>
        <Grid
          container
          spacing={0}
          alignItems="center"
          justify="space-between"
          wrap="nowrap"
        >
          <Grid container spacing={4} alignItems="center" wrap="nowrap">
            <Grid item>
              <Logo />
            </Grid>
            <Grid item>
              <SiteTitle />
            </Grid>
          </Grid>
          <Grid container spacing={4} alignItems="center" justify="flex-end">
            <Menu currentPage={currentPage} />
          </Grid>
        </Grid>
      </Toolbar>
    </AppBar>
  );
};

Header.propTypes = propTypes;
Header.defaultProps = defaultProps;

export default Header;
export { propTypes as HeaderPropTypes, defaultProps as HeaderDefaultProps };
