import React from "react";
import PropTypes from "prop-types";
import shortid from "shortid";

import { Link as RouterLink } from "react-router-dom";
import Link from "@material-ui/core/Link";
import { makeStyles } from "@material-ui/core/styles";

import { convertMenuToRoute } from "../Menu";

import Tabs from "@material-ui/core/Tabs";
import Tab from "@material-ui/core/Tab";
import Button from "@material-ui/core/Button";

import i18n from "../../i18n";
//import { useTranslation } from "react-i18next";
import { en_en } from "./MenuHorizontal.lang.en-en";
import { nl_nl } from "./MenuHorizontal.lang.nl-nl";
import { nl_be } from "./MenuHorizontal.lang.nl-be";

i18n.addResourceBundle("en-EN", "MenuHorizontal", en_en);
i18n.addResourceBundle("nl-NL", "MenuHorizontal", nl_nl);
i18n.addResourceBundle("nl-BE", "MenuHorizontal", nl_be);

/**
 * Defines the prop types
 */
const propTypes = {
  items: PropTypes.arrayOf(PropTypes.string),
  currentPage: PropTypes.string
};

/**
 * Defines the default props
 */
const defaultProps = {
  items: ["Features", "Pricing", "Contact", "Login", "Free trial", "En"],
  currentPage: "Features"
};

/**
 * Styles the component
 */
const useStyles = makeStyles(theme => ({
  // Tried many many ways, but couldn't change the color
  link: {
    color: theme.palette.primary.contrastText
  }
}));

/**
 * Displays the component
 */
const MenuHorizontal = props => {
  const { items, currentPage } = props;

  const classes = useStyles();
  const { link } = classes;

  const LinkToRoute = React.forwardRef((props, ref) => (
    <Link
      component={RouterLink}
      color="primary"
      underline="none"
      ref={ref}
      {...props}
    />
  ));

  const tabs = items.map(item =>
    item === "Free trial" ? (
      <Tab
        key={shortid.generate()}
        label={item}
        component={Button}
        variant="contained"
        color="secondary"
        to={convertMenuToRoute(item)}
      />
    ) : (
      <Tab
        className={link}
        key={shortid.generate()}
        label={item}
        component={LinkToRoute}
        to={convertMenuToRoute(item)}
      />
    )
  );

  const currentMenuItem = items.findIndex(item => item === currentPage);

  /**
   * No need to make the menu scrollable
   * - These few menu items fit well on 1280px
   * - When set scrollable sometimes the arrows are shown even when it's not necessary
   */
  return (
    <Tabs
      className="MenuHorizontal"
      value={currentMenuItem}
      textColor="secondary"
    >
      {tabs}
    </Tabs>
  );
};

MenuHorizontal.propTypes = propTypes;
MenuHorizontal.defaultProps = defaultProps;

export default MenuHorizontal;
export {
  propTypes as MenuHorizontalPropTypes,
  defaultProps as MenuHorizontalDefaultProps
};
