import React from "react";
import PropTypes from "prop-types";

import { useForm } from "react-hook-form";

import Input from "../../Input";

import Grid from "@material-ui/core/Grid";
import { makeStyles } from "@material-ui/core/styles";

import Card from "@material-ui/core/Card";
import CardContent from "@material-ui/core/CardContent";
import Button from "@material-ui/core/Button";
import Typography from "@material-ui/core/Typography";

import i18n from "../../../i18n";
import { useTranslation } from "react-i18next";
import { en_en } from "./ContactForm.lang.en-en";
import { nl_nl } from "./ContactForm.lang.nl-nl";
import { nl_be } from "./ContactForm.lang.nl-be";

i18n.addResourceBundle("en-EN", "ContactForm", en_en);
i18n.addResourceBundle("nl-NL", "ContactForm", nl_nl);
i18n.addResourceBundle("nl-BE", "ContactForm", nl_be);

/**
 * Defines the prop types
 */
const propTypes = {
  defaultValues: PropTypes.shape({
    name: PropTypes.string,
    email: PropTypes.string,
    description: PropTypes.string
  }),
  onSubmit: PropTypes.func
};

/**
 * Defines the default props
 */
const defaultProps = {
  defaultValues: {
    name: "",
    email: "",
    description: ""
  },
  onSubmit: () => console.log("ContactForm on submit")
};

/**
 * Styles the component
 */
const useStyles = makeStyles(theme => ({
  card: {
    [theme.breakpoints.down("sm")]: {
      backgroundColor: "transparent",
      boxShadow: "none"
    }
  },

  subtitle1: {
    maxWidth: theme.finster.typography.textWidthSubtitle1
  }
}));

/**
 * Displays the component
 */
const ContactForm = props => {
  const { defaultValues, onSubmit } = props;

  const { t } = useTranslation("ContactForm");

  const classes = useStyles();
  const { card, subtitle1 } = classes;

  const { register, handleSubmit, formState } = useForm({
    defaultValues
  });

  return (
    <Card className={card}>
      <CardContent>
        <Grid container spacing={1}>
          <Grid item>
            <Typography variant="h4">Send us a message</Typography>
          </Grid>
          <Grid item>
            <Typography variant="subtitle1" className={subtitle1}>
              Nulla eleifend pulvinar purus, molestie euismod odio imperdiet ac.
              Ut sit amet erat nec nibh rhoncus varius in non lorem.
            </Typography>
          </Grid>
          <Grid item>
            <form>
              <Grid container spacing={1}>
                <Grid item xs={12} lg={6}>
                  <Input
                    type="text"
                    fullWidth
                    inputRef={register()}
                    inputText={{
                      name: "name",
                      label: t("Your name"),
                      required: true
                    }}
                  />
                </Grid>
                <Grid item xs={12} lg={6}>
                  <Input
                    type="email"
                    fullWidth
                    inputRef={register}
                    inputEmail={{
                      name: "email",
                      label: t("Your email"),
                      required: true
                    }}
                  />
                </Grid>
                <Grid item xs={12}>
                  <Input
                    type="textArea"
                    fullWidth
                    inputRef={register()}
                    inputTextArea={{
                      name: "description",
                      label: t("Description"),
                      required: true
                    }}
                  />
                </Grid>
                <Grid item xs={12} lg>
                  <Button
                    disabled={!formState.isValid && formState.isSubmitted}
                    variant="contained"
                    color="primary"
                    onClick={handleSubmit(onSubmit)}
                  >
                    {t("submit")}
                  </Button>
                </Grid>
              </Grid>
            </form>
          </Grid>
        </Grid>
      </CardContent>
    </Card>
  );
};

ContactForm.propTypes = propTypes;
ContactForm.defaultProps = defaultProps;

export default ContactForm;
export {
  propTypes as ContactFormPropTypes,
  defaultProps as ContactFormDefaultProps
};
